<template>
  <div id="app">
    <!--  -->
    <transition :name="routerAnimate">
      <!-- 1) -->
      <keep-alive v-if="keepAlive">
        <router-view />
      </keep-alive>
      <!-- 2) -->
      <router-view v-if="!keepAlive" />
    </transition>
    <!--  -->
    <NavBar v-show="isNavBar" />
  </div>
</template>
<script>
import NavBar from "./components/NavBar.vue";
import { checkUser } from "@/api/user/users";
import { getPurseInfo } from "@/api/user/purse";
import { mapState } from "vuex";
export default {
  components: { NavBar },
  data() {
    return {
      routerAnimate: "toback",
      keepAlive: false,
      isNavBar: false,
    };
  },
  created() {
    // HELLO
    console.log(
      "%c    ",
      [
        "padding:10px 10px;",
        "padding-left:0;",
        `background:url(${require("@/assets/image/favicon.png")}) left center no-repeat;`,
      ].join("")
    );

    // 
    this.isNavBar = !(this.$route.meta.lv !== 1);
    // 
    const locationToken =
      localStorage.getItem(this.$store.state.TOKEN_NAME) || "";
    const sessionToken =
      sessionStorage.getItem(this.$store.state.TOKEN_NAME) || "";
    if (locationToken === "" && sessionToken === "") return;
    let token = locationToken || sessionToken;
    // token
    checkUser(token)
      .then((res) => {
        if (res.data.code === 20011) {
          // 
          this.$store.commit("setLoginState", true);
          this.$store.commit("setToken", {
            token,
            savePwd: locationToken !== "",
          });
          // store
          this.$store.commit("setUserInfo", res.data.data);
          // 
          this.reqPurseInfo();
        }
      })
      .catch(() => {
        this.$store.commit("loginOut");
      });
  },

  watch: {
    // 
    $route(to, from) {
      // 
      this.keepAlive = to.meta?.keepAlive ?? false;
      // 
      this.isNavBar = !(to.meta.lv !== 1);

      if (to.params?.animate) {
        switch (to.params?.animate) {
          // 
          case "forward":
            this.routerAnimate =
              to.meta?.lv > from.meta?.lv
                ? `${to.params.animate}2`
                : to.params.animate;
            break;
          case "toup":
            this.routerAnimate = to.params.animate;
            break;
          // 
          default:
            this.routerAnimate = to.params.animate;
            break;
        }

        // 
      } else {
        switch (this.routerAnimate) {
          // 
          case "toup":
            this.routerAnimate = "toDown";
            break;
          default:
            //  2->1
            this.routerAnimate =
              to.meta?.lv < from.meta?.lv ? "toback2" : "toback";
            break;
        }
      }
    },

    token(value) {
      if (!value) {
        this.$store.commit("setLoginState", false);
      }
    },
  },

  methods: {
    // 
    async reqPurseInfo() {
      const res = await getPurseInfo(this.$store.getters.token);
      // console.log(res);
      if (res.status == 200 && res.data.code === 20011) {
        this.$store.commit("setPurseInfo", res.data.data);
      }
    },
  },
  computed: {
    ...mapState(["token"]),
  },
};
</script>
<style>
@import url("/public/css/default.css");
.animate_animated {
  will-change: transition aniamtion;
}
/*  */
.forward-enter-active,
.forward-leave-active {
  width: 100%;
  transition: all var(--router-delay);
  position: absolute;
  opacity: 1;
}
.forward-leave-to {
  opacity: 0;
  transform: translateX(-100%);
}
.forward-enter {
  opacity: 0;
  transform: translateX(100%);
}

/*  */
.forward2-enter-active,
.forward2-leave-active {
  width: 100%;
  transition: all var(--router-delay);
  position: absolute;
  opacity: 1;
}
.forward2-leave-to {
  opacity: 0;
  filter: blur(8px) grayscale(60%);
  transform: translateX(0);
}
.forward2-enter {
  opacity: 1;
  transform: translateX(100%);
}

/*  */
.toback-enter-active,
.toback-leave-active {
  opacity: 1;
  transition: all var(--router-delay);
  position: absolute;
  width: 100%;
}
.toback-leave-to {
  transform: translateX(100%);
}
.toback-enter {
  opacity: 0;
  transform: translateX(-100%);
}
/*  */
.toback2-enter-active,
.toback2-leave-active {
  opacity: 1;
  transition: all var(--router-delay);
  position: absolute;
  width: 100%;
}
.toback2-leave-to {
  opacity: 0.8;
  filter: blur(4px);
  transform: translateX(100%);
}
.toback2-enter {
  opacity: 0;
  transform: translateX(0%);
}

/*  */
.toup-enter-active,
.toup-leave-active {
  opacity: 1;
  transition: all calc(var(--router-delay));
  position: absolute;
  width: 100%;
}
.toup-leave-to {
  opacity: 0;
  transform: translateY(-10%) scale(0.8);
  filter: blur(10px);
  box-shadow: var(--shadow-color2);
}
.toup-enter {
  opacity: 0;
  transform: translateY(100%) scale(1);
}
/*  */
.toDown-leave-active {
  z-index: 2000;
  transition: all calc(var(--router-delay)) ease-in;
  position: absolute;
  transform: scale(0.8);
  width: 100%;
}
.toDown-enter-active {
  transition: all calc(var(--router-delay)) ease;
  position: absolute;
  transform: none;
  width: 100%;
}
.toDown-leave-to {
  opacity: 0;
  backdrop-filter: blur(20px);
  filter: blur(6px);
  transform: translateY(100%);
  box-shadow: var(--shadow-color2);
}
.toDown-enter {
  transform: scale(1);
}
</style>
