<template>
  <div class="MenuList">
    <menu-item
      v-for="(p, i) in option"
      :title="p.title"
      :target-name="p.targetName"
      :key="i"
    />
    <button class="v-btn lg-off" @click="cannelLogin"></button>
  </div>
</template>
<script>
import { Dialog } from "vant";
import MenuItem from "./MenuItem.vue";
export default {
  name: "MenuList",
  components: { MenuItem },
  data() {
    return {
      option: [
        { title: "", targetName: "user" },
        { title: "", targetName: "userinfo" },
        { title: "", targetName: "option" },
      ],
    };
  },
  methods: {
    // 
    cannelLogin() {
      Dialog.confirm({
        title: "",
        beforeClose: (action, done) => {
          if (action === "confirm") {
            setTimeout(() => {
              this.$store.commit("loginOut"); // 
              done(); // 
            }, 1500);
          } else {
            done(); // 
          }
        },
      }).catch(() => {});
    },
  },
};
</script>
<style scoped>
.MenuList {
  margin-top: 0.5rem;
  padding: 0 0.3rem;
}
.lg-off {
  margin-top: 0.4rem;
  width: 100%;
  padding-top: 0.2rem;
  padding-bottom: 0.2rem;
  font-size: 0.45rem;
}
</style>