<template>
  <div class="ChangePhone">
    <!--  -->
    <div class="first" v-if="!isUser">
      <span class="title"> {{ getPhoneFormat }} </span>
      <van-field
        v-model="oldCode"
        type="number"
        size="6"
        left-icon="label-o"
        placeholder=" "
        class="v-input code-input"
        :rules="[{ required: true }]"
      >
        <template #button>
          <!--  -->
          <input
            type="button"
            class="right-btn"
            @click="getLoginCode"
            :disabled="codeFontDis"
            v-model="codeFont"
            :style="codeFontDis ? 'opacity:0.7;' : 'opacity:1;'"
        /></template>
      </van-field>
      <!--  -->
      <div class="btn-group">
        <input
          @click="toLoginByReg"
          type="button"
          value=""
          class="v-btn"
        />
      </div>
    </div>
    <!--  -->
    <div class="second" v-if="isUser">
      <span class="title"></span>
      <van-field
        v-model="newPhone"
        type="tel"
        size="11"
        left-icon="label-o"
        placeholder=" "
        class="v-input code-input"
        :rules="[{ required: true }]"
      />
      <van-field
        v-model="newCode"
        type="number"
        size="6"
        left-icon="label-o"
        placeholder=" "
        class="v-input code-input"
        :rules="[{ required: true }]"
      >
        <template #button>
          <!--  -->
          <input
            type="button"
            class="right-btn"
            @click="getLoginCode"
            :disabled="codeFontDis"
            v-model="codeFont"
            :style="codeFontDis ? 'opacity:0.7;' : 'opacity:1;'"
        /></template>
      </van-field>
      <!--  -->
      <div class="btn-group">
        <input
          @click="updatePhone"
          type="button"
          value=""
          class="v-btn"
        />
      </div>
    </div>
  </div>
</template>

<script>
import {
  loginByCode,
  getLoginCode,
  updatePhone,
  getUpdateCode,
} from "@/api/user/users";
import { mapState } from "vuex";
export default {
  name: "ChangePhone",
  data() {
    return {
      // 
      isUser: false,
      oldCode: "",

      timer: "",
      // 
      newPhone: "",
      newCode: "",
      codeFont: "",
      codeFontDis: false,
      phoneReg:
        /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/,
    };
  },
  computed: {
    ...mapState(["userInfo"]),
    getPhoneFormat() {
      return this.userInfo.phone.replace(/^(\d{3})\d{4}(\d{4})$/, "$1****$2");
    },
  },
  methods: {
    // 1
    async getLoginCode() {
      if (this.timer !== "") {
        return;
      }
      if (!this.phoneReg.test(this.newPhone) && this.isUser) {
        return this.$toast("");
      }

      if (this.userInfo.phone === this.newPhone) {
        return this.$toast("");
      }
      this.codeBtnInit(); // 

      //  or 
      const res = this.isUser
        ? await getUpdateCode(this.newPhone, this.$store.getters.token)
        : await getLoginCode(this.userInfo.phone);
      if (res.status === 200 && res.data.code === 20011) {
        this.$notify({
          type: "success",
          message: ``,
          duration: 5000,
        });
        // this.$notify({
        //   type: "success",
        //   message: `\n${res.data.data}`,
        //   duration: 5000,
        // });
        // 
        // copyTextAsync(res.data.data)
        //   .then(() => {
        //     this.$toast("");
        //   })
        //   .catch();
        // if (!this.isUser) {
        //   // 
        //   this.oldCode = res.data.data; // 
        // } else {
        //   // 
        //   this.newCode = res.data.data; // 
        // }
      } else {
        if (this.isUser) {
          this.$notify({
            type: "danger",
            message: ``,
          });
        } else {
          this.$notify({
            type: "danger",
            message: ``,
          });
        }
      }
    },

    // 2) 
    async toLoginByReg() {
      // 
      const res = await loginByCode(this.userInfo.phone, this.oldCode);
      if (res.status === 200 && res.data.code === 20011) {
        this.isUser = true; // 
        this.$notify({
          type: "success",
          message: ``,
          duration: 1000,
        });
        // token
        this.$store.commit("setToken", { token: res.data.data });
      } else {
        this.$notify({ type: "danger", message: "" });
      }
    },

    // 3) 
    async updatePhone() {
      this.$toast.loading({ duration: 0, forbidClick: true });
      const res = await updatePhone(
        this.newPhone,
        this.newCode,
        this.$store.getters.token
      );
      this.$toast.clear();
      // console.log(res.data);
      if (res.status === 200 && res.dat.code === 20011) {
        this.$notify({ type: "success", message: "" });
        this.$emit("input", false);
        this.$set(this.userInfo, "phone", this.newPhone);
        this.init();
      } else {
        this.$notify({ type: "danger", message: "" });
      }
    },

    // 
    codeBtnInit() {
      this.codeFontDis = true;
      let times = 30;
      this.timer = setInterval(() => {
        times--;
        this.codeFont = times + "s";
        if (times === 0) {
          this.reCodeBtnInit();
        } else {
          this.codeFont = times + "s";
        }
      }, 1000);
    },
    // 
    reCodeBtnInit() {
      this.codeFont = "";
      this.codeFontDis = false;
      clearInterval(this.timer);
      this.timer = "";
    },
    // 
    init() {
      this.reCodeBtnInit();
      this.isUser = false;
      this.oldCode = "";
      clearInterval(this.timer);
      this.timer = "";
      this.newPhone = ""; // 
      this.newCode = "";
      this.codeFont = "";
      this.codeFontDis = false;
    },
  },

  watch: {
    // 
    isUser() {
      this.reCodeBtnInit();
    },
  },
};
</script>

<style scoped>
.first .title {
  padding: 0.5rem 0.3rem;
}
.btn-group {
  width: 100%;
}
</style>