<template>
  <default-page :title="getTitle" class="check-order">
    <div class="contain" v-if="!isError">
      <!--  -->
      <div
        class="v-card flex-col order-info"
        v-if="getTitle !== '' && $route.query?.isOrder"
      >
        <div style="font-weight: 600"></div>
        <van-steps :active="orderStateIndex" active-color="var(--tip-color2)">
          <van-step name="0"></van-step>
          <van-step name="1"></van-step>
          <van-step name="2"></van-step>
          <van-step name="3"></van-step>
          <van-step name="4"></van-step>
        </van-steps>
        <span class="btm">{{ info.time }}</span>
      </div>
      <!-- 1 -->
      <div v-if="!emptyAddress" @click="isEdit ? showAddressSelectPanel() : ''">
        <address-card
          class="v-card v-click"
          :item="defaultAddress"
          :readonly="true"
        >
          <template #right-icon>
            <van-icon
              v-show="isEdit"
              name="arrow"
              class="icons"
              size="0.6rem"
              color="var(--text-color3)"
            />
          </template>
        </address-card>
      </div>
      <!--  -->
      <div class="v-card address" v-if="emptyAddress" @click="toAddressView">
        <van-icon
          size="1.2rem"
          name="location"
          class="flex-center-center default"
        />
        <div class="text"></div>
      </div>

      <!-- 2+ -->
      <div class="v-card" v-if="goodsList.length > 0">
        <!--  -->
        <goods-info
          class="goods-item"
          v-for="p in goodsList"
          :key="p.id"
          :goods="p"
          :price="p?.price"
          :props="p?.props"
          :num="p?.quantity"
        />
        <!-- \ -->
        <div class="label">
          <menu-item
            title=""
            :value="`${allPostage > 0 ? allPostage + '' : ''}`"
          />
          <menu-item title="">
            <van-field
              type="text"
              class="remarks"
              input-align="right"
              placeholder=""
              :disabled="!isEdit"
              v-model="remarks"
              maxlength="30"
              :border="false"
              :clearable="true"
            />
          </menu-item>
        </div>
      </div>

      <!-- 3 -->
      <div class="v-card all-price">
        <div class="title"></div>
        <div class="lable-group">
          <span>
            <small>{{ goodsLengths }}</small>
          </span>
          <div class="blank"><small></small>{{ getPostage }}</div>
        </div>
        <div class="lable-group">
          <span>
            <small>{{ goodsLengths }}</small>
          </span>
          <div class="blank"><small></small>{{ prices }}</div>
        </div>
        <!--  -->
        <div class="lable-group">
          <span>
            <small
              >{{ purseInfo?.points }} 1001</small
            ></span
          >
          <div
            v-if="getPointsVal !== ''"
            class="lable"
            :style="isPoints ? 'color: var(--tip-color2)' : ''"
          >
            {{
              $store.state.purseInfo?.points > 0
                ? `${getPointsVal}`
                : ""
            }}&nbsp;
            <van-checkbox
              v-if="!isReadonly"
              v-model="isPoints"
              :disabled="$store.state.purseInfo?.points <= 0"
              checked-color="var(--tip-color2)"
            />
          </div>
        </div>
      </div>

      <!--  -->
      <div class="v-card">
        <div class="allPrice reduces" v-show="isPoints">
          <small></small>
          <small class="reduce-price">-{{ getPointsVal }}</small>
        </div>
        <div class="allPrice">
          <span></span>
          <span style="color: var(--tip-color2); font-size: 0.5rem"
            ><small style="color: var(--tip-color2)"></small
            >{{ finallyPrice }}</span
          >
        </div>
      </div>

      <!-- 4 -->
      <van-cell-group inset class="group v-card" v-if="isReadonly">
        <van-cell
          @click="copyId(info?.orderId)"
          title=""
          :value="info?.orderId"
        />
        <van-cell title="" :value="''" />
        <van-cell title="" :value="info?.time" />
      </van-cell-group>

      <!-- 5 -->
      <van-radio-group
        v-if="!isReadonly"
        v-model="selectPay"
        class="v-card pay-list"
      >
        <van-cell-group :border="false">
          <van-cell
            v-for="p in payType"
            :key="p.type"
            :title="p.name"
            clickable
            :border="false"
            @click="selectPay = p.type"
          >
            <template #icon>
              <img
                :src="require(`@/assets/image/icon/${p.icon}`)"
                class="icon"
              />
            </template>
            <template #right-icon>
              <van-radio
                checked-color="var(--tip-color2)"
                :name="p.type"
                :disabled="!p.open"
              />
            </template>
          </van-cell>
        </van-cell-group>
      </van-radio-group>

      <!--  -->
      <van-submit-bar
        style="animation: 1.2s fadeInUp"
        v-if="!isReadonly"
        :disabled="emptyAddress"
        :price="Number(finallyPrice.value * 100)"
        :button-text="submitText"
        @submit="submitBtnFun"
      >
        <template #default>
          <van-popover
            v-show="isSubmit"
            v-model="showPopover"
            trigger="click"
            placement="top-start"
          >
            <div class="edot-popiup" style="padding: 0.2rem; font-size: 0.5rem">
              <div
                class="edit v-click"
                style="padding: 0.2rem"
                @click="isEdit = !isEdit"
              >
                {{ isEdit ? "" : "" }}
              </div>
              <div
                class="cancel v-click"
                style="padding: 0.2rem"
                @click="cancelOrder"
              >
                
              </div>
            </div>
            <template #reference>
              <van-icon name="ellipsis" size="0.5rem" class="bar-left" />
            </template>
          </van-popover>
        </template>
      </van-submit-bar>
    </div>
    <!--  -->
    <select-address
      :addressList="addressList"
      v-model="showselect"
      :defaultAddress="defaultAddress"
      @changeAddress="changeAddress"
    ></select-address>
    <!--  -->
    <van-popup
      transition="popup"
      v-model="showPayPanel"
      closeable
      round
      position="center"
      class="flex-col pay-panel"
    >
      <label class="title"></label>
      <div class="price">
        <span></span>
        <span class="big">{{ finallyPrice }}</span>
      </div>
      <div class="flex-center-center purse">
        <img
          :src="require('@/assets/image/icon/balance_icon.png')"
          class="icon"
        />
        <small> {{ balance }} </small>
      </div>
      <van-button
        class="v-btn"
        @click="payOrder"
        :disabled="purseInfo.balance < finallyPrice.value"
        >{{
          purseInfo.balance < finallyPrice.value ? "" : ""
        }}</van-button
      >
    </van-popup>
    <!--  -->
    <error-card
      v-if="isError"
      @refresh="$router.back()"
      btn-text=""
      image="error"
      text=""
    />
  </default-page>
</template>
<script>
import DefaultPage from "@/components/DefaultPage";
import ErrorCard from "@/components/ErrorCard";
import { getResourImageByName } from "@/api/res";
import {
  cancelOrderById,
  payOrder,
  submitOrder,
  updateOrder,
} from "@/api/order/order";
import { getDefaultAddress, getAllAddress } from "@/api/user/address";
import AddressCard from "@/components/Address/AddressCard.vue";
import SelectAddress from "@/components/Address/SelectAddress";
import GoodsInfo from "@/components/Goods/GoodsInfo.vue";
import MenuItem from "@/components/My/MenuItem.vue";
import { checkText } from "@/util/xxsFilter";
import currency from "currency.js";
import { getPurseInfo } from "@/api/user/purse";
import { copyTextAsync } from "@/util/copy";
import { mapState } from "vuex";
export default {
  name: "CheckOrder",
  components: {
    ErrorCard,
    DefaultPage,
    GoodsInfo,
    AddressCard,
    MenuItem,
    SelectAddress,
  },
  data() {
    return {
      // 
      info: [],
      goodsList: [],
      defaultAddress: {},
      addressList: [], // 
      orderType: "",
      // 
      orderStateIndex: 0, // 
      goodsLengths: 0, // 
      remarks: "", // 
      allPrice: 0,
      allPostage: 0,
      finallyPrice: 0,
      balance: 0,
      // 
      getTitle: "",
      isError: false, // 
      submitError: false, // 
      showPayPanel: false, // 
      showPopover: false, // 
      isReadonly: false,
      isPoints: false, // 
      // 
      emptyAddress: false, // 
      showselect: false, // 
      // 
      isEdit: true, // 
      isSubmit: false, // 
      isPayDone: false, // 

      orderState: ["", ""], // 1) isEdit f su

      // 
      submitId: "", // id
      submitText: "",
      // 
      selectPay: "purse",
      payType: [
        { type: "purse", icon: "balance_icon.png", name: "", open: true },
        { type: "wechat", icon: "wechat.png", name: "", open: false },
        { type: "zfb", icon: "zfb.png", name: "", open: false },
        { type: "ysf", icon: "ysf.png", name: "", open: false },
      ],

      // 
      reason: "",
    };
  },
  created() {
    // 
    this.getBalance(); // 
    const q = this.$route.query;
    // 
    if (q.info === "[object Object]" || q.goodsList === "[object Object]") {
      return (this.isError = true);
    }
    this.orderType = q.orderType || ""; // 
    // 1
    if (q.isOrder && q.info.orderId) {
      this.getAddressList(); // 
      this.checkOrderType(); // 
      this.submitId = q.info.orderId; // id
      // console.log(q);
      const { name, phone, address } = q.info;
      this.defaultAddress.name = name;
      this.defaultAddress.phone = phone;
      this.defaultAddress.address = address;
      this.defaultAddress.id = null;
      this.remarks = q.info.remarks;
      // 
      q.goodsList.forEach((goods) => {
        this.goodsLengths = this.goodsLengths + goods.quantity;
        goods.props =
          (goods.color || "") +
          (goods.size || "") +
          (goods.combo || "") +
          (goods.edition || "");
        this.goodsList.push(goods);
        this.allPostage = currency(this.allPostage).add(goods.postage);
        this.allPrice = currency(this.allPrice).add(goods.price);
      });
      for (const key in q.info) {
        this.$set(this.info, key, q.info[key]);
      }
      // 
    } else {
      // 
      this.getDefaultAddress();
      //  
      q.goodsList.forEach((goods) => {
        this.goodsList.push(goods);
        this.goodsLengths = this.goodsLengths + goods.quantity;
        this.allPrice = this.allPrice + goods.unitPrice * goods.quantity;
        this.allPostage = this.allPostage + goods.postage * goods.quantity;
      });
      // 
      for (const key in q?.info) {
        this.$set(this.info, key, q.info[key]);
      }
    }
    // = +
    this.finallyPrice = currency(this.allPrice).add(this.allPostage);
    this.allPrice *= 100;
  },
  methods: {
    // 
    showAddressSelectPanel() {
      this.showselect = true;
    },
    changeAddress(p) {
      for (const key in p) {
        this.$set(this.defaultAddress, key, p[key]);
      }
    },

    // 
    async getDefaultAddress() {
      const res = await getDefaultAddress(this.$store.getters.token);
      if (res.status === 200) {
        // 
        if (res.data.code === 20011 && res.data.data.length > 0) {
          this.getAddressList(false); //
          for (const key in res.data.data) {
            this.$set(this.defaultAddress, key, res.data.data[key]);
          }
          // 
        } else {
          this.getAddressList(true); // 
          return;
        }
      } else {
        this.isError = true;
      }
    },

    //  
    async getAddressList(getFirst = false) {
      const res = await getAllAddress(this.$store.getters.token);

      // 
      if (
        res.data.code !== 20011 ||
        res.status !== 200 ||
        res.data.data.length === 0
      )
        return (this.emptyAddress = true);
      //
      const data = res.data.data;
      if (data.length > 0 && getFirst) {
        for (const key in data[0]) {
          this.$set(this.defaultAddress, key, data[0][key]);
        }
      }
      // 
      this.addressList.splice(0);
      data.forEach((p) => {
        this.addressList.push(p);
      });
    },

    // 
    checkOrderType() {
      this.isSubmit = true;
      this.submitId = this.info?.orderId;
      this.isEdit = false;
      switch (this.orderType) {
        case "unpaid":
          this.getTitle = "";
          this.submitText = "";
          this.orderStateIndex = 0;
          break;
        case "update":
          this.isEdit = true;
          this.submitText = "";
          this.orderStateIndex = 0;
          break;
        case "undeliver":
          this.getTitle = "";
          this.orderStateIndex = 1;
          break;
        case "delivered":
          this.getTitle = "";
          this.orderStateIndex = 2;
          break;
        case "uncomment":
          this.getTitle = "";
          this.orderStateIndex = 3;
          break;
        case "commented":
          this.getTitle = "";
          this.orderStateIndex = 4;
          break;
        default:
          this.getTitle = "";
          break;
      }
    },

    // ID
    copyId(text) {
      copyTextAsync(text)
        .then(() => {
          this.$toast({ message: "", position: "bottom" });
        })
        .catch(() => {
          this.$toast({ message: "", position: "bottom" });
        });
    },

    // 
    submitBtnFun() {
      if (this.orderType && this.isSubmit != "") {
        switch (this.orderType) {
          case "unpaid":
            this.showPayPanel = true;
            break;
          case "update":
            this.updateOrders(); // 
            break;
          case "undeliver":
            this.submitText = "undeliver";
            break;
          case "delivered":
            this.submitText = "delivered";
            break;
          case "comment":
            break;
        }
      } else {
        // 
        this.isSubmit ? this.updateOrders() : this.onPutOrder();
      }
    },

    // 1
    onPutOrder() {
      if (this.emptyAddress || !this.defaultAddress.id || this.isSubmit)
        return this.$toast("");
      const aid = this.defaultAddress.id; // id
      const items = [];
      // id * 
      this.goodsList.forEach((p) => {
        items.push({ gid: p.id, quantity: p.quantity });
      });
      this.$toast.loading({
        message: "...",
        forbidClick: true,
        onOpened: async () => {
          // 
          const res = await submitOrder(
            aid,
            items,
            checkText(this.remarks),
            this.$store.getters.token
          );
          if (res.status === 200 && res.data.code === 20011) {
            this.submitId = res.data.data;
            this.isEdit = false; // 
            this.isSubmit = true;
            this.submitError = false;
            this.submitText = "";
          } else {
            this.submitError = true;
          }
        },
        // 
        onClose: () => {
          if (this.submitError) {
            this.showPayPanel = false;
            this.$toast({ type: "fail", message: "" });
          } else {
            this.showPayPanel = true;
          }
        },
      });
    },

    // 2) 
    payOrder() {
      this.showPayPanel = false;

      if (this.submitId === "") return;
      this.$toast.loading({
        message: "...",
        forbidClick: true,
        onOpened: async () => {
          let ponits = 0;
          if (this.isPoints) {
            ponits =
              this.getPointsVal.intValue > this.allPrice
                ? this.allPrice
                : this.getPointsVal.intValue / 100;
          }
          const res = await payOrder(
            this.submitId,
            ponits,
            this.$store.getters.token
          );
          this.isPayDone = res.data.code === 20011;
          this.showPayPanel = false;
        },
        onClose: () => {
          if (this.isPayDone) {
            this.$toast({
              type: "success",
              message: "!",
              forbidClick: true,
            });
            this.$router.replace({
              name: "order",
              params: { animate: "forward" },
            });
          } else {
            this.$toast({
              type: "fail",
              message: "",
              forbidClick: true,
            });
          }
        },
      });
    },

    // 3) 
    async updateOrders() {
      if (this.isEdit) {
        if (this.submitId === "") return;
        const res = await updateOrder(
          this.submitId,
          this.defaultAddress.id || null,
          checkText(this.remarks) || null,
          this.$store.getters.token
        );
        if (res.status === 200 && res.data.code === 20011) {
          this.showPayPanel = true;
          this.submitId = res.data.data?.orderId;
        } else {
          this.$toast("");
        }
      } else {
        this.showPayPanel = true;
      }
    },
    // 4) 
    async cancelOrder() {
      if (this.submitId === "") return;
      this.$dialog
        .confirm({
          title: "",
          beforeClose: (action, done) => {
            if (action === "confirm") {
              setTimeout(async () => {
                const res = await cancelOrderById(
                  this.submitId,
                  this.reason, // 
                  this.$store.getters.token
                );
                if (res.status === 200 && res.data.code === 20011) {
                  this.$toast({ message: "", type: "success" });
                  this.$router.back();
                } else {
                  this.$toast("");
                }
                done();
              }, 1000);
            }
            done();
          },
        })
        .catch(() => {});
    },

    // 
    getBalance() {
      getPurseInfo(this.$store.getters.token)
        .then((res) => {
          if (res.data.code === 20011) {
            this.balance = currency(res.data.data.balance);
            this.$store.commit("setPurseInfo", res.data.data);
          }
        })
        .catch(() => {});
    },

    // 
    toAddressView() {
      this.$router.push({
        name: "address",
        params: {
          animate: "forward",
          toBack: true,
        },
      });
    },
    // 
    getImgUrl(url) {
      return getResourImageByName(url);
    },
  },
  computed: {
    ...mapState(["purseInfo"]),
    // 
    prices() {
      return currency(this.allPrice / 100);
    },

    // 
    getPostage() {
      if (this.allPostage) {
        return currency(this.allPostage);
      } else {
        return "";
      }
    },

    // 
    getPointsVal() {
      if (this.$route.query.isOrder && this.isReadonly) {
        return "";
      } else {
        if (
          this.purseInfo.points >
          (this.allPrice / 100 + this.allPostage) * 100
        ) {
          return currency(this.allPrice / 100).add(this.allPostage);
        } else {
          return currency(this.purseInfo.points / 100);
        }
      }
    },
  },
  watch: {
    defaultAddress: {
      deep: true,
      immediate: true,
      handler() {
        this.emptyAddress = this.defaultAddress?.id === "";
      },
    },

    // 
    orderType(val) {
      if (val !== "") {
        this.isReadonly = !(val === "update" || val === "unpaid");
      } else {
        this.isReadonly = false;
      }
    },

    // 
    isPoints(val) {
      if (val) {
        this.finallyPrice = currency(this.finallyPrice).subtract(
          this.getPointsVal
        );
      } else {
        this.finallyPrice = currency(this.allPrice / 100).add(this.allPostage);
      }
    },
  },
};
</script>
<style scoped>
.check-order {
  background-color: #eeeeee5e;
  min-height: 100vh;
  padding: 0;
}
.contain {
  padding: 0.1rem;
  margin-bottom: 1.4rem;
}
.v-card {
  transition: all 0.3s;
  border-radius: 10px;
  box-shadow: var(--shadow-color3);
  background-color: var(--theme-color);
}
.address {
  display: flex;
  align-items: center;
  padding: 0.4rem;
  font-size: 0.42rem;
  font-size: 600;
  cursor: pointer;
}

.contain .address-card {
  margin-bottom: 0.36rem;
}
.address .default {
  margin: 0 0.2rem;
  color: var(--tip-color2);
}
.order-info >>> .van-steps {
  padding: 0;
  margin-top: 0.3rem;
}
.order-info >>> .van-step__title {
  font-size: 0.3rem;
}
.order-info .btm {
  font-size: 0.35rem;
  color: var(--text-color3);
}
.top-order {
  display: flex;
  align-items: center;
}
.top-order .left {
  margin: 0.2rem;
}
.top-order .right div {
  margin-bottom: 0.2rem;
}
.top-order .right .top {
  color: var(--tip-color2);
}

/*  */
.v-card {
  margin-bottom: 0.36rem;
}
/*  */
.remarks {
  float: left;
  width: 3rem;
  background-color: transparent;
  padding: 0;
}
.remarks >>> input::placeholder {
  text-align: right;
  padding-right: 0.2rem;
  letter-spacing: 0.05rem;
  font-size: 0.36rem;
  color: var(--text-color3);
}
.goods-item {
  margin: 0.2rem 0;
}
/*  */
.all-price .title {
  font-weight: 600;
  margin-bottom: 0.2rem;
}

/*  */
.pay-list >>> .van-cell {
  padding: 0.2rem 0;
}
.pay-list .icon {
  display: block;
  width: 0.6rem;
  height: 0.6rem;
  object-fit: cover;
  margin-right: 0.3rem;
}
.van-cell__title >>> span {
  font-size: 0.4rem;
}

/* pay-panel */
.pay-panel {
  height: 7rem;
  width: 90%;
  padding: 0.4rem 0.3rem;
  padding-bottom: 0.6rem;
  align-items: center;
  justify-content: space-between;
}
.pay-panel .title {
  display: block;
  width: 100%;
  text-align: center;
  font-size: 0.55rem;
  margin: 0.4rem 0;
}
.pay-panel >>> .van-popup__close-icon {
  top: 0.4rem;
  right: 0.4rem;
}
.pay-panel .price {
  margin: 0.2rem 0;
  text-align: center;
}
.pay-panel .price span {
  font-weight: 600;
  font-size: 0.8rem;
}
.pay-panel .price .big {
  font-size: 1.2rem;
}
.pay-panel .icon {
  height: 2em;
  margin: 0.2rem;
}

/*  */
.bar-left {
  cursor: pointer;
  padding: 0 0.2rem;
}
.check-order >>> .edit-popup >>> .item {
  padding: 0.2rem;
}

/*  */
.group {
  width: 100%;
  margin: 0.4rem 0;
}
.reduces {
  padding: 0.1rem;
}
.reduce-price {
  padding: 0.1rem 0;
}
.allPrice {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.group >>> .van-cell {
  padding: 0.2rem 0;
}
.check-order >>> .van-submit-bar__button {
  width: auto;
}
</style>