<template>
  <!--  -->
  <van-list
    class="animate__animated animate__fadeIn good-list"
    v-model="loading"
    :error.sync="isHttpError"
    :finished="finished"
    finished-text=""
    @load="onLoad"
  >
    <!--  -->
    <good-card
      :item="item"
      v-for="item in goodsList"
      :key="item.id"
      class="item"
    />
  </van-list>
</template>
<script>
import { getGoodsListByPageSize } from "@/api/good/good";
import { getEventGoodsById } from "@/api/res";
import GoodCard from "./GoodCard.vue";
export default {
  components: { GoodCard },
  props: {
    reqType: {
      // 
      type: String,
      required: false,
      default: "type",
    },
    type: {
      // 
      type: Object,
      required: false,
    },
  },
  name: "GoodsList",
  data() {
    return {
      currentPage: 1, // 
      goodsList: [], // 
      isHttpError: false, // 
      loading: false, // 
      finished: false, // 
    };
  },
  methods: {
    // 
    onLoad() {
      switch (this.reqType) {
        case "type": //  or 
          this.reqGoodsByType();
          break;
        case "eid": // 
          this.reqGoodsByEid();
          break;
      }
    },
    // 
    reqGoodsByType() {
      if (this.type === undefined) return;
      this.loading = true;
      getGoodsListByPageSize(this.currentPage, 8, this.type)
        .then((res) => {
          if (res.data.code === 20011) {
            const data = res.data.data;
            data.records.map((p) => {
              p.goods.images = p.goods.images.split(",");
              this.goodsList.push(p);
            });

            // 
            this.loading = false; // 

            // 
            if (data.total >= this.goodsList.length) {
              // console.log(this.currentPage, data.pages);// 
              this.finished = true;
            } else {
              this.currentPage = data.current;
              this.currentPage++;
            }
          } else {
            this.isHttpError = true;
          }
        })
        .catch(() => {
          this.isHttpError = true;
        });
    },

    // 
    reqGoodsByEid() {
      if (!this.$route.query?.eid) return;

      this.loading = true;
      // 
      getEventGoodsById(this.currentPage, 8, this.$route.query?.eid)
        .then((res) => {
          // console.log(res.data);
          if (res.status === 200 && res.data.code === 20011) {
            const data = res.data.data;
            data.records.forEach((p) => {
              p.goods.images = p.goods.images.split(",");
              this.goodsList.push(p);
            });

            // 
            this.loading = false; // 

            // 
            if (this.goodsList.length === data.total) {
              this.finished = true;
            } else {
              this.currentPage = data.current;
              this.currentPage++; // 
            }
          } else {
            this.isHttpError = true;
          }
        })
        .catch(() => {
          this.isHttpError = true;
        });
    },

    // 
    reStart() {
      new Promise((res) => {
        this.currentPage = 1;
        this.goodsList.splice(0); // 
        return res(true);
      }).then(() => {
        this.onLoad();
      });
    },
  },
  watch: {
    type(newVal, oldVal) {
      // 
      if (newVal.saleSort !== oldVal.saleSort) {
        this.reStart();
      }
      // 
      if (newVal.priceSort !== oldVal.priceSort) {
        this.reStart();
      }
    },
  },
};
</script>

<style scoped>
/*  */
.good-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 0.2rem;
  padding: 0.1rem;
  animation-delay: var(--router-delay) / 2;
}
.good-list .item {
  width: 48%;
}
.good-list >>> .van-list__loading,
.good-list >>> .van-list__finished-text,
.good-list >>> .van-list__error-text {
  width: 100%;
  font-size: 0.35rem;
}
</style>