<template>
  <div>
    <!--  -->
    <TopNav :title="title" @click-left="onClickLeft" />

    <!--  -->
    <div class="froms">
      <van-form @submit="disabled ? '' : toRegister()">
        <van-field
          :disabled="disabled"
          v-model="username"
          name="username"
          type="text"
          class="v-input"
          left-icon="contact"
          placeholder=" "
          :rules="[{ required: true }]"
          @blur="checkUsername"
        />
        <van-field
          v-model="phone"
          :disabled="disabled"
          type="tel"
          name="phone"
          class="v-input"
          left-icon="phone-o"
          placeholder=" "
          :rules="[{ required: true }]"
          @blur="phoneCheck"
        />
        <van-field
          :disabled="disabled"
          v-model="code"
          type="number"
          name="code"
          left-icon="label-o"
          placeholder=" "
          class="v-input"
          :rules="[{ required: true }]"
        >
          <template #button>
            <!--  -->
            <input
              type="button"
              class="right-btn"
              @click="getRegisterCode"
              :disabled="codeFontDis || disabled"
              v-model="codeFont"
              :style="codeFontDis ? 'opacity:0.7;' : 'opacity:1;'"
            />
          </template>
        </van-field>
        <van-field
          v-model="password"
          :disabled="disabled"
          type="password"
          name="password"
          class="v-input"
          left-icon="eye-o"
          placeholder=" "
          :rules="[{ required: true }]"
        />
        <input
          class="v-btn"
          :disabled="disabled"
          style="width: 100%; padding: 0.3rem; font-size: 0.45rem"
          type="submit"
          value=""
        />
      </van-form>
    </div>
  </div>
</template>
<script>
import { register, getRegisterCode } from "@/api/user/users";

import TopNav from "@/components/TopNav.vue";
import { Notify } from "vant";
export default {
  name: "RegisterFrom",
  components: { TopNav },
  data() {
    return {
      disabled: false, // 
      title: " ",
      username: "",
      phone: "",
      code: "",
      password: "",
      usernameReg: /^[a-zA-Z]/,
      phoneReg:
        /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/,
      codeFont: "",
      codeFontDis: false,
    };
  },
  methods: {
    // 
    async toRegister() {
      if (!this.checkUsername()) return;
      if (!this.phoneCheck()) return;
      const res = await register({
        username: this.username.trim(),
        phone: this.phone.trim(),
        code: this.code.trim(),
        password: this.password.trim(),
      });
      // console.log(res.data);
      if (res.data.code === 20011) {
        this.disabled = true;
        Notify({ type: "success", message: "" });
        setTimeout(() => {
          this.$router.replace({
            name: "login",
            params: { username: this.username },
          }); // 
          this.disabled = false;
        }, 2000);
      } else {
        Notify({ type: "danger", message: "" });
      }
    },
    // 
    async getRegisterCode() {
      if (!this.phone) {
        return Notify("");
      } else if (!this.phoneCheck()) {
        return Notify("");
      }
      this.codeFontDis = true;
      let times = 60;
      let timer = setInterval(() => {
        if (times === 1) {
          this.codeFont = "";
          clearInterval(timer);
        }
        times--;
        if (times === 0) {
          this.codeFont = "";
          this.codeFontDis = false;
          clearInterval(timer);
        } else {
          this.codeFont = times + "s";
        }
      }, 1000);
      const res = await getRegisterCode(this.phone);
      // console.log(res.data);
      if (res.data.code === 20011) {
        Notify({
          type: "success",
          // message: `\n${res.data.data}`,
          message: `,`,
        });
        // this.code = res.data.data; // 
      } else {
        Notify({
          type: "danger",
          message: ``,
        });
      }
    },

    // 
    phoneCheck() {
      if (this.phone !== "" && !this.phoneReg.test(this.phone.trim())) {
        Notify({ type: "danger", message: "" });
        return false;
      }
      return this.phoneReg.test(this.phone.trim());
    },
    checkUsername() {
      if (
        this.username !== "" &&
        !this.usernameReg.test(this.username.trim())
      ) {
        Notify({ type: "danger", message: "" });
        return false;
      }
      return this.usernameReg.test(this.username.trim());
    },
    // 
    onClickLeft() {
      this.$router.replace({
        name: "login",
        params: { animate: "toback" },
      });
    },
  },
};
</script>
<style scoped></style>
