import { request } from "@/util/request";

/**
 * 
 * @param {string} token
 * @returns
 */
export function setUserSignIn(token) {
  return request({
    method: "post",
    url: "/users/signIn",
    headers: {
      Authorization: token,
    },
  });
}

/**
 * 
 * @param {string} token
 * @returns boolean
 */
export function isTodaySignIn(token) {
  return request({
    method: "get",
    url: "/users/signIn/today",
    headers: {
      Authorization: token,
    },
  });
}

/**
 * 
 * @param {string} token
 * @returns
 */
export function getSignDays(token) {
  return request({
    method: "get",
    url: "/users/signIn/count",
    headers: {
      Authorization: token,
    },
  });
}
