import { request } from "@/util/request";

/**
 * 
 * @param {String} fid
 * @param {*} token
 * @returns
 */
export function getCommentSons(fid, token) {
  return request({
    method: "get",
    url: `/comments/query`,
    headers: {
      Authorization: token,
    },
    params: {
      id: fid,
    },
  });
}
/**
 * 
 * @param {number} evaluationId id
 * @param {number} fid   null
 * @param {string} content 
 * @param {token} token
 * @returns
 */
export function addCommentChild(evaluationId, fid, content, token) {
  if (!fid) fid = null; // 
  return request({
    method: "post",
    url: `/comments/save`,
    headers: {
      Authorization: token,
    },
    data: {
      evaluationId,
      fid,
      content,
    },
  });
}

/**
 *  
 * @param {string} fid id
 * @param {string} token
 * @returns
 */
export function addCommentlikedByfid(fid, token) {
  return request({
    method: "put",
    url: `/comments/liked/${fid}`,
    headers: {
      Authorization: token,
    },
  });
}

/**
 *  
 * @param {string} id kd
 * @param {string} token
 * @returns
 */
export function deleteCommentByfid(id, token) {
  return request({
    method: "delete",
    url: `/comments/delete/${id}`,
    headers: {
      Authorization: token,
    },
  });
}
