<template>
  <div class="purse-view">
    <top-nav :title="''" @click-left="$router.back()" />
    <!--  -->
    <main v-if="!isError">
      <!--  -->
      <div class="v-card top-card animate__animated animate__fadeInUp">
        <div class="left">
          <label></label>
          <label @click="reqPurseInfo" class="btm">
            <span class="tp"></span>
            <span class="big" v-show="!isLodaing">{{
              Number(this.purseInfo?.balance).toFixed(2) || "0.00"
            }}</span>
            <van-loading
              class="load"
              v-show="isLodaing"
              color="var(--text-color)"
              size="0.6rem"
            />
          </label>
        </div>
        <div class="right">
          <img src="@/assets/image/icon/balance_icon.png" class="bg" />
        </div>
      </div>
      <!--  -->
      <div class="bottoms">
        <!--  -->
        <div class="center-title">
          <label></label>
          <span>:</span>
        </div>
        <!--  -->
        <div class="combo">
          <div
            @click="toReCharge(1, p.price, p.points, p.id)"
            class="v-click v-card item"
            v-for="p in chargeCombo"
            :key="p.id"
          >
            <div class="price">{{ p?.price }}</div>
            <div class="active">
              
              <span style="color: var(--tip-color)">{{ p?.points }}</span>
              
            </div>
          </div>
          <!--  -->
          <div
            @click="showCustomCharge = true"
            class="v-click v-card item"
            :key="2333"
            style="opacity: 0.8"
          >
            <div class="price" style="font-size: 0.4rem"></div>
            <div class="active">
              <span style="color: var(--tip-color)"></span>
            </div>
          </div>
        </div>
      </div>
    </main>
    <div class="btm-text" v-show="!isError">
      &emsp;&emsp;
    </div>

    <!--  -->
    <van-popup
      transition="popup"
      v-model="showCustomCharge"
      class="v-card user-popup"
    >
      <span class="title"></span>
      <small>:</small>
      <van-field
        class="v-input"
        placeholder=""
        v-model="customValues"
        type="number"
      />
      <div class="btn-group">
        <button class="v-btn v-cancel" @click="showCustomCharge = false">
          
        </button>
        <button class="v-btn" @click="toReCharge(0, customValues)"></button>
      </div>
    </van-popup>
    <!--  -->
    <error-card
      v-if="isError"
      @refresh="window.reload"
      :image="'network'"
      text=""
    />
  </div>
</template>
<script scoped>
import {
  getPurseInfo,
  reChargeByBombo,
  reChargeByValue,
} from "@/api/user/purse";
import { getRechargeCombo } from "@/api/user/purse";
import TopNav from "@/components/TopNav.vue";
import ErrorCard from "@/components/ErrorCard.vue";
import { Dialog, Notify } from "vant";
import { mapState } from "vuex";
export default {
  components: { TopNav, ErrorCard },
  name: "PurseView",
  data() {
    return {
      chargeCombo: [], // 
      isLodaing: true, // 
      showCustomCharge: false, // 
      isError: false, // 404
      timer: "",
      customValues: "",
    };
  },
  computed: { ...mapState(["purseInfo"]) },
  mounted() {
    // 
    this.reqPurseInfo();
    // 
    this.getCombo();
  },
  methods: {
    // 
    reqPurseInfo() {
      if (this.timer !== "") return;
      this.isLodaing = true;
      this.timer = setTimeout(async () => {
        const res = await getPurseInfo(this.$store.getters.token);
        if (res.status == 200 && res.data.code === 20011) {
          this.$store.commit("setPurseInfo", res.data.data);
          this.timer = "";
          this.isLodaing = false;
          return;
        }
        this.isError = true;
      }, 800);
    },
    // 
    async getCombo() {
      const res = await getRechargeCombo(this.$store.getters.token);
      if (res.status === 200 && res.data.code === 20011) {
        this.chargeCombo = res.data.data;
        return;
      }
      this.isError = true;
    },

    // 
    toReCharge(type, price, points, id) {
      let msg = points ? `,${points}` : "";
      Dialog.confirm({
        title: "",
        message: `${price}${msg}`,
        beforeClose: (action, done) => {
          if (action === "confirm") {
            setTimeout(async () => {
              //    
              const res = type
                ? await reChargeByBombo(type, id, this.$store.getters.token)
                : await reChargeByValue(
                    type,
                    this.customValues,
                    this.$store.getters.token
                  );
              // console.log(res);
              if (res.status === 200 && res.data.code === 20011) {
                this.showCustomCharge = false;
                this.customValues = "";
                Notify({ message: "", type: "success" });
                this.reqPurseInfo(); // 
              } else {
                Notify({ message: "", type: "danger" });
              }
              done(); // 
            }, 1500);
          } else {
            done(); // 
          }
        },
      }).catch(() => {});
    },
  },
};
</script>
  
<style scoped>
.purse-view {
  padding: 0.2rem 0;
  overflow: hidden;
  user-select: none;
}
/*  */
.top-card {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.6rem 0.8rem;
  font-weight: 600;
  color: var(--text-color);
  background-color: var(--theme-color2);
  margin: 0.4rem 0.3rem;
  margin-bottom: 0;
  transition: 0.4s;
  height: 3.8rem;
  border-radius: 10px 10px 0 0;
}
.top-card .left {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  transition: 0.4s;
}
.top-card .left label {
  padding: 0.1rem 0;
  display: inline-block;
}
.top-card .left .btm {
  height: 1rem;
  display: flex;
  align-items: flex-end;
}
.top-card .left .big {
  margin-bottom: -0.2rem;
}
.top-card .load {
  display: inline-block;
}
.top-card .left .big {
  font-size: 0.9rem;
  font-family: sans-serif;
  cursor: pointer;
}
.top-card .right img {
  display: block;
  width: 2rem;
  object-fit: contain;
}

/*  */
.bottoms {
  margin-top: -0.4rem;
  position: relative;
  box-shadow: rgba(128, 128, 128, 0.3) 0 -2px 4px;
  border-radius: 10px 10px 0 0;
  padding: 0.3rem 0.2rem;
  background-color: var(--theme-color);
}
.combo {
  display: grid;
  justify-content: space-around;
  grid-template-columns: repeat(3, 3.2rem);
}
.combo .item {
  padding: 0.2rem 0.2rem;
  border: 1px solid var(--border-color);
  background-color: var(--theme-color);
  border-radius: 8px;
  margin: 0.2rem;
  text-align: center;
  cursor: pointer;
}
.item .price {
  font-weight: 600;
  font-size: 0.5rem;
  color: var(--theme-color4);
  padding-bottom: 0.1rem;
}
.item .active {
  font-size: 0.3rem;
  color: var(--text-color3);
}
.btm-text {
  width: 100%;
  text-align: center;
  color: var(--text-color3);
  opacity: 0.6;
  font-size: 0.2rem;
}
.center-title {
  width: 100%;
  padding: 0.2rem;
}
.center-title span {
  font-size: 0.32rem;
}

/*  */
.user-popup {
  width: 80%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  padding-top: 0.5rem;
}
.user-popup .title {
  font-size: 0.45rem;
  font-weight: 600;
  width: 100%;
  text-align: center;
}
.user-popup .v-input {
  width: 82%;
  border: 1px solid var(--border-color);
  background-color: var(--bg-color5);
  margin-top: 0.4rem;
  padding-left: 0.4rem;
}
.user-popup .btn-group {
  width: 100%;
}
</style>