import { request } from "@/util/request";

// 
export function getNoticeByPage(page, size) {
  return request({
    method: "post",
    url: `/notices/query/${page}/${size}`,
  });
}

// 
export function getIndexNotice() {
  return request({
    method: "get",
    url: "/notices/index",
  });
}
