<template>
  <div class="add-comment-view">
    <!--  -->
    <top-nav title="" class="top-nav" @click-left="$router.back()">
      <template #rblock>
        <!--  -->
        <button class="v-btn submit-btn" @click="submitComment"></button>
      </template>
    </top-nav>
    <!--  -->
    <div class="content-froms" v-if="!isError">
      <comment-form
        v-for="p in orderInfo?.orderItems"
        :key="p.orderItemId"
        :goods="p"
        ref="commentForm"
      />
    </div>
    <!--  -->
    <error-card
      v-if="isError"
      @refresh="$router.back()"
      btn-text=""
      image="network"
      text=""
    />
  </div>
</template>
  
  <script>
import TopNav from "@/components/TopNav.vue";
import ErrorCard from "@/components/ErrorCard.vue";
import CommentForm from "@/components/Comment/CommentForm.vue";
import { getOrderById } from "@/api/order/order";
import { checkText } from "@/util/xxsFilter";
import { addOrderComment } from "@/api/comment/ordercomment";
export default {
  components: { TopNav, ErrorCard, CommentForm },
  name: "AddCommentView",
  data() {
    return {
      orderId: "", // id
      orderInfo: "",
      // 
      isError: false, // 
    };
  },
  mounted() {
    // id
    if (this.$route.query?.id) {
      this.orderId = this.$route.query?.id;
      this.$toast.loading({
        forbidClick: true,
        message: "...",
        duration: 0,
      });
      getOrderById(this.orderId, this.$store.getters.token).then((res) => {
        if (res.status === 200 && res.data.code === 20011) {
          this.isError = false;
          res.data.data?.orderItems.forEach((p) => {
            p.rate = 0;
          });
          this.orderInfo = res.data.data;
        } else {
          this.isError = true;
        }
      });
    } else {
      this.isError = true;
    }
    this.$toast.clear(true);
  },
  methods: {
    // 
    submitComment() {
      if (this.isError) return;
      this.$toast.loading({ duration: 0, forbidClick: true });
      //  
      this.$refs.commentForm.forEach(async (p) => {
        if (p?.content === "") return this.$toast(" ");
        const res = await addOrderComment(
          p.orderItemId,
          p.rate, // 
          p.imgUrl, // 
          checkText(p?.content), // xxs
          this.$store.getters.token
        );
        if (res.status === 200 && res.data.code === 20011) {
          this.$toast.success(" ");
          // 
          this.$router.replace({
            name: "order",
            params: {
              animate: "forward",
              toBack: false,
            },
          });
        } else {
          this.$toast.fail(" !");
        }
        // 
        setTimeout(() => {
          this.$toast.clear();
        }, 300);
      });
    },
  },
};
</script>
<style scoped>
.add-comment-view {
  background-color: var(--bg-color6);
}
/*  */
.top-nav {
  border-bottom: 1px solid var(--border-color);
  background-color: var(--theme-color);
}
.top-nav .submit-btn {
  width: auto;
  border-radius: 6px;
  padding: 0.05rem 0.15rem;
  padding-left: 0.2rem;
  font-size: 0.3rem;
  text-align: center;
  box-shadow: none;
}
/*  */
.content-froms {
  width: 100%;
}
</style>