<template >
  <div class="serch-toggle">
    <!--  -->
    <div class="topNav">
      <div
        class="nav-item"
        :class="active === i ? 'active' : ''"
        v-for="(p, i) in option"
        :key="i"
        @click="changeNav(i)"
      >
        <span>{{ p.title }}</span>
      </div>
    </div>
    <van-tabs
      class="tabs"
      v-model="active"
      swipeable
      sticky
      animated
      line-width="1.4rem"
      title-active-color="var(--theme-color2)"
      color="var(--theme-color2)"
    >
      <!--  -->
      <van-tab v-for="(p, i) in option" :key="i">
        <!--  -->
        <goods-list :type="{ ...p.type }" />
      </van-tab>
    </van-tabs>
  </div>
</template>
<script>
import GoodsList from "@/components/GoodsList.vue";
export default {
  name: "SearchToggle",
  components: { GoodsList },
  props: ["keyWords", "cid"],
  data() {
    return {
      active: 0,
      isSaleDes: true, // 
      isPriceDes: false, // 
      option: [
        {
          title: "",
          type: { cid: this.cid, name: this.keyWords || "" },
        },
        {
          title: "",
          type: { cid: this.cid, name: this.keyWords || "", isNew: 1 },
        },
        {
          title: "",
          type: { cid: this.cid, name: this.keyWords || "", saleSort: "Des" },
        },
        {
          title: "",
          type: { cid: this.cid, name: this.keyWords || "", priceSort: "Asc" },
        },
      ],
    };
  },
  // mounted() {
  //   console.log(this.option);
  // },
  methods: {
    changeNav(i) {
      if (this.active == i) {
        if (i == 2) this.isSaleDes = !this.isSaleDes;
        if (i == 3) this.isPriceDes = !this.isPriceDes;
      } else {
        this.active = i;
      }
    },
  },
  watch: {
    // 
    isSaleDes() {
      this.$set(
        this.option[2],
        "title",
        this.isSaleDes ? "" : ""
      );
      this.$set(
        this.option[2].type,
        "saleSort",
        this.isSaleDes ? "Des" : "Asc"
      );
    },
    isPriceDes() {
      this.$set(
        this.option[3],
        "title",
        this.isPriceDes ? "" : ""
      );
      this.$set(
        this.option[3].type,
        "priceSort",
        this.isPriceDes ? "Des" : "Asc"
      );
      this.$set(this.option[3], "priceSort", this.isPriceDes ? "Des" : "Asc");
    },
  },
};
</script>
<style scoped>
.tabs {
  width: 100%;
  height: auto;
  margin-top: -50px;
}
.topNav {
  display: flex;
  justify-content: space-around;
  padding: 0.2rem 0;
  user-select: none;
  width: 100%;
  position: relative;
  cursor: pointer;
  z-index: 1;
}
.nav-item {
  flex: 1;
  margin: 0.2rem;
  text-align: center;
  transition: 0.4s;
}
.nav-item span {
  color: rgba(0, 0, 0, 0.6);
  font-size: 0.4rem;
  transition: 0.4s;
  position: relative;
}
.nav-item.active span {
  color: var(--theme-color4);
  transition: 0.4s;
}
</style>