<template >
  <div class="contian">
    <div class="shop-view" v-if="$store.state.isLoginState">
      <!--  -->
      <div class="top">
        <span class="title">
          <span class="big"></span>
          <small> ({{ getCartCount }})</small>
        </span>
        <!--  -->
        <span
          class="edit"
          style="
            margin-left: auto;
            padding-right: 0.2rem;
            color: var(--text-color3);
          "
          @click="deleteAllCart"
        >
          
        </span>
        <span
          class="edit"
          :style="isEdit ? 'color:var(--tip-color2)' : ''"
          @click="isEdit = !isEdit"
          >{{ isEdit ? "" : "" }}</span
        >
      </div>
      <!--  -->
      <cart-list
        ref="cardList"
        :selectAll="selectAll"
        :isEdit="isEdit"
        @changeSelectArr="changeSelectArr"
        @getCartLength="getCartLength"
        @changeSelectAll="changeSelectAll"
      />
      <!-- 1 -->
      <van-submit-bar
        v-show="!isEdit"
        class="bar submits"
        :price="getFinallPrices"
        button-text=""
        @submit="onSubmitOrder"
      >
        <van-checkbox
          v-model="selectAll"
          @click="onChangeSelectAll"
          checked-color="var(--tip-color2)"
          ></van-checkbox
        >
      </van-submit-bar>
      <!-- 2 -->
      <van-submit-bar
        v-show="isEdit"
        class="bar deletes"
        button-text=""
        button-color="var(--tip-color2)"
        @submit="deleteShopCart"
      >
        <van-checkbox
          v-model="selectAll"
          checked-color="var(--tip-color2)"
          style="margin-right: auto"
          @click="onChangeSelectAll"
          ></van-checkbox
        >
      </van-submit-bar>
    </div>
    <!--  -->
    <div
      class="toLogin flex-center-center flex-col"
      v-if="!$store.state.isLoginState"
    >
      <button
        class="v-btn v-click animate__tada animate__tada"
        style="width: auto"
        @click="toLogin"
      >
        
      </button>
      <div class="title"> ~</div>
    </div>
  </div>
</template>
<script>
import CartList from "@/components/ShopCart/CartList.vue";
import { clearAllShopCart, deleteShopcartByIds } from "@/api/shopcart/shopcart";
export default {
  components: { CartList },
  name: "ShopcarView",
  data() {
    return {
      selectList: [], // 
      cartList: [], // 
      isEdit: false, // 
      selectAll: false, // 
      finallPrice: 0,
      counts: 0,
    };
  },
  computed: {
    getFinallPrices() {
      return this.finallPrice * 100;
    },
    getCartCount() {
      return this.cartList.length;
    },
  },
  methods: {
    // 
    getCartLength(cartList) {
      this.cartList = cartList;
    },
    // 
    changeSelectAll(val) {
      this.selectAll = val;
    },
    //  
    onChangeSelectAll() {
      this.$refs.cardList.selectList.splice(0);
      if (this.selectAll) {
        this.cartList.forEach((p, i) => {
          this.$refs.cardList.selectList.push(i);
        });
      } else {
        this.$refs.cardList.selectList.splice(0);
      }
    },
    // 
    changeSelectArr(selectList) {
      this.finallPrice = 0; // 
      this.selectList = selectList;
      // 
      this.selectList.forEach((i) => {
        this.finallPrice =
          this.finallPrice +
          (this.cartList[i].unitPrice + this.cartList[i].postage) *
            this.cartList[i].quantity;
      });
    },

    // 
    async onSubmitOrder() {
      if (this.selectList.length <= 0) return this.$toast(" ");
      // 
      const goodsList = [];
      this.selectList.forEach((i) => {
        goodsList.push({
          ...this.cartList[i],
          id: this.cartList[i].goodsItemId, // id
          gid: this.cartList[i].goodsId,
          images: this.cartList[i].icon,
          price: this.cartList[i].unitPrice,
        });
      });
      this.$router.push({
        name: "checkorder",
        params: {
          animate: "forward",
          toBack: "true",
        },
        query: {
          // 
          info: {
            price: this.finallPrice, // 
          },
          goodsList,
        },
      });
    },

    // 
    async deleteShopCart() {
      if (this.selectList.length <= 0) return this.$toast(" ");
      // id
      let ids = [];
      this.selectList.forEach((p) => {
        ids.push(this.cartList[p].id);
      });
      this.$dialog
        .confirm({
          title: " ?",
          beforeClose: async (action, done) => {
            if (action === "confirm") {
              const res = await deleteShopcartByIds(
                ids,
                this.$store.getters.token
              );
              // console.log(res.data);
              if (res.status === 200 && res.data.code === 20011) {
                this.selectList.forEach((p) => {
                  this.$children[0].cartList.splice(p, 1);
                });
                this.$children[0].selectList.splice(0);
                done();
                this.cartList = this.$toast({
                  type: "success",
                  message: "!",
                });
              } else {
                this.$toast({ type: "fail", message: "!" });
              }
            } else {
              done();
            }
          },
        })
        .catch(() => {});
    },
    // 
    async deleteAllCart() {
      // console.log(this.$children);
      this.$dialog
        .confirm({
          title: " ?",
          beforeClose: async (action, done) => {
            if (action === "confirm") {
              const res = await clearAllShopCart(this.$store.getters.token);
              // console.log(res.data);
              if (res.status === 200 && res.data.code === 20011) {
                this.$children[0]?.cartList?.splice(0);
                this.$children[0]?.selectList?.splice(0);
                done();
                this.cartList = this.$toast({
                  type: "success",
                  message: "!",
                });
              } else {
                this.$toast({ type: "fail", message: "!" });
              }
            } else {
              done();
            }
          },
        })
        .catch(() => {});
    },

    toLogin() {
      this.$router.push({
        name: "login",
        params: {
          animate: "forward",
          toBack: "true",
        },
      });
    },
  },
};
</script>
  <style scoped>
/*  */
.toLogin {
  width: 100%;
  height: 90vh;
}
.toLogin .title {
  padding: 0.2rem;
}
/*  */
.shop-view {
  width: 100%;
  min-height: 100vh;
}
.bar {
  position: fixed;
  bottom: 1.6rem;
  box-shadow: var(--shadow-color3);
}
/*  */
.top {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  padding: 0.3rem;
  background-color: transparent;
  backdrop-filter: blur(30px);
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  user-select: none;
}
.top .title .big {
  cursor: pointer;
  font-size: 0.5rem;
  font-weight: 600;
}
.top .edit {
  font-size: 0.35rem;
  cursor: pointer;
}
/*  */
.submits {
  animation: 0.5s fadeInUp;
}
.deletes {
  animation: 0.5s fadeInUp;
}
</style>