import { request } from "@/util/request";
import qs from "qs";
/**
 * id
 * @param {*} token
 * @returns
 */
export function getAllShopCart(token) {
  return request({
    method: "get",
    url: `/shoppingCart/query`,
    headers: {
      Authorization: token,
    },
  });
}
/**
 * id
 * @param {*} id
 * @param {*} token
 * @returns
 */
export function getShopCartById(id, token) {
  return request({
    method: "get",
    url: `/shoppingCart/query/${id}`,
    headers: {
      Authorization: token,
    },
  });
}

/**
 * 
 * @param {number} gid
 * @param {number} quantity
 * @param {string} token
 * @returns
 */
export function addShopCart(gid, quantity, token) {
  return request({
    method: "post",
    url: "/shoppingCart/save",
    headers: {
      Authorization: token,
    },
    data: {
      gid,
      quantity,
    },
  });
}

/**
 * 
 * @param {number} id id
 * @param {number} goodsItemId id
 * @param {number} quantity
 * @param {string} token
 * @returns
 */
export function updateShopCartProps(id, goodsItemId, quantity, token) {
  return request({
    method: "put",
    url: "/shoppingCart/update",
    headers: {
      Authorization: token,
    },
    data: { id, goodsItemId, quantity },
  });
}

/**
 * 
 * @param {number} id id
 * @param {string} token
 * @returns
 */
export function deleteOneShopCart(id, token) {
  return request({
    method: "delete",
    url: `/shoppingCart/delete/${id}`,
    headers: {
      Authorization: token,
    },
  });
}

/**
 * 
 * @param {array} ids array
 * @param {number} quantity
 * @param {string} token
 * @returns
 */
export function deleteShopcartByIds(ids, token) {
  return request({
    method: "delete",
    url: `/shoppingCart/deletes`,
    headers: {
      Authorization: token,
    },
    params: { ids },
    paramsSerializer: {
      serialize(params) {
        return qs.stringify(params, { indices: false });
      },
    },
  });
}

/**
 * 
 * @param {string} token
 * @returns
 */
export function clearAllShopCart(token) {
  return request({
    method: "delete",
    url: "/shoppingCart/empty",
    headers: {
      Authorization: token,
    },
  });
}
