<template>
  <div class="comment-popup">
    <van-popup
      v-model="showPopup"
      position="bottom"
      round
      :style="{ width: '100%', height: '70%' }"
    >
      <div class="comment-box">
        <div style="text-align: center">{{ commentLength }} </div>
        <!--  -->
        <transition-group tag="div" :name="isAnimate ? 'sliceInZoomOut' : ''">
          <comment-child-card
            @deleteComment="deleteComment"
            @toComment="changeCommentObj"
            v-for="(p, i) in comment_child"
            :comment="p"
            :key="p.id"
            :id="p.id"
            :first="i"
          >
            <template>
              <!--  -->
              <div class="son-card">
                <transition-group tag="div" name="sliceInZoomOut">
                  <comment-child-card
                    v-for="(son, j) in p.childComments"
                    :comment="son"
                    @deleteComment="deleteComment"
                    @toComment="changeCommentObj"
                    :key="son.id"
                    :id="p.id"
                    :fid="p?.id"
                    :first="i"
                    :second="j"
                  ></comment-child-card>
                </transition-group>
              </div>
            </template>
          </comment-child-card>
        </transition-group>
      </div>
      <!--  -->
      <div class="to-comment" ref="toCommentBar" @mouseleave="clearCommentObj">
        <van-image
          lazy-load
          round
          :src="getImgSrc($store.userInfo?.userIcon)"
          class="user-icon"
        />
        <van-field
          class="v-input"
          type="text"
          v-model="commentText"
          :formatter="formatInput"
          :placeholder="placeText || ''"
          ref="commentInput"
          :clearable="true"
          @keyup.enter="addComment"
        ></van-field>
        <input type="submit" value="" class="v-btn" @click="addComment" />
      </div>
    </van-popup>
  </div>
</template>

<script>
import {
  addCommentChild,
  deleteCommentByfid,
  getCommentSons,
} from "@/api/comment/pickcomment";
import CommentChildCard from "@/components/Comment/CommentChildCard.vue";
import { getResourImageByName } from "@/api/res";
import { checkText } from "@/util/xxsFilter";
import { mapState } from "vuex";
export default {
  name: "CommentDetail",
  components: { CommentChildCard },
  data() {
    return {
      commentText: "",
      placeText: "", // 
      fid: "",
      sid: "",

      commentLength: 0, // 
      // 
      firstIndex: -1,
      secondIndex: -1,

      commentNickName: "",
      comment_child: [], // 

      // 
      showPopup: false,
      isAnimate: true, // 
    };
  },
  mounted() {
    this.showPopup = this.showCommentPopup;
  },
  methods: {
    // 
    async reqGetItem() {
      this.isAnimate = false;
      this.clearCommentObj(); // 
      const res = await getCommentSons(
        this.commentId,
        this.$store.getters.token
      );
      if (res.status === 200 && res.data.code === 20011) {
        this.comment_child.splice(0); // 
        this.commentLength = 0;
        this.commentLength += res.data.data.length;
        res.data.data.forEach((p, i) => {
          this.comment_child.push(p);
          if (!p?.childComments) return;
          if (res.data.data.length === i + 1) {
            this.isAnimate = true;
          }
          p?.childComments.filter((item) => {
            this.commentLength++;
            item.content = item.content.replace(
              /(^@.*?:)/,
              "<span class='at-comment'>$1</span>"
              // "<span style='color:var(--tip-color2);'>$1</span>"
            );
            return item;
          });
        });
      }
    },

    // 
    async addComment() {
      if (this.commentText.trim() === "")
        return this.$toast("");
      const res = await addCommentChild(
        this.commentId,
        this.fid,
        this.commentText.trim(),
        this.$store.getters.token
      );
      this.commentText = "";
      if (res.data.code === 20011 && res.status === 200) {
        // 
        this.reqGetItem();
        this.$toast("");
      } else {
        this.$toast("");
      }
      this.clearCommentObj(); // 
    },

    // 
    async deleteComment(id) {
      this.$dialog
        .confirm({
          title: "",
          beforeClose: async (action, done) => {
            if (action === "confirm") {
              const res = await deleteCommentByfid(
                id,
                this.$store.getters.token
              );
              if (res.data.code === 20011 && res.status === 200) {
                this.$toast("");
                this.reqGetItem(); // 
              } else {
                this.$toast("");
              }
            }
            done();
          },
        })
        .catch(() => {});
      // beforeClose:
    },
    // 
    formatInput(val) {
      return checkText(val);
    },

    // 
    getImgSrc() {
      return getResourImageByName(this.$store.state.userInfo.icon);
    },

    // 
    changeCommentObj(obj) {
      let { fid, sid, firstIndex, secondIndex } = obj;
      // console.log(obj);
      this.fid = fid;
      this.sid = sid;
      this.firstIndex = firstIndex;
      this.secondIndex = secondIndex;
      this.commentNickName = obj.name;
      this.$refs.commentInput.$refs.input.focus();
    },

    // 
    clearCommentObj() {
      this.firstIndex = -1;
      this.secondIndex = -1;
      this.fid = null;
      this.commentNickName = "";
    },
  },
  computed: {
    ...mapState(["showCommentPopup", "commentId"]),
  },
  watch: {
    // 
    firstIndex() {
      if (this.commentNickName && this.firstIndex >= 0) {
        if (this.commentNickName === this.$store.state.userInfo?.nickname) {
          this.commentText = "@:";
          this.placeText = "@:";
        } else {
          this.commentText = `@${this.commentNickName}:`;
          this.placeText = `@${this.commentNickName}`;
        }
      } else {
        this.placeText = "";
      }
    },
    // 
    secondIndex() {
      if (this.commentNickName && this.secondIndex >= 0) {
        if (this.commentNickName === this.$store.state.userInfo?.nickname) {
          this.placeText = this.commentText = "@:";
        } else {
          this.placeText = this.commentText = `@${this.commentNickName}:`;
        }
      } else {
        this.commentText = "";
      }
    },
    // 
    commentText(val) {
      if (val.trim() === "") {
        this.clearCommentObj();
      }
    },

    // id
    commentId(newVal, oldVal) {
      if (newVal !== oldVal && newVal !== undefined) {
        this.isAnimate = false;
        this.reqGetItem();
      }
    },
    // 
    showCommentPopup(val) {
      this.showPopup = val;
    },
    // 
    showPopup(val) {
      this.$store.commit("setShowCommentPopup", {
        show: val,
        commentId: this.commentId,
      });
      if (val) {
        this.reqGetItem();
      }
    },
  },
};
</script>
<style scoped>
.author {
  padding: 0.2rem;
}
/*  */
.comment-box {
  border-radius: 20px 20px 0 0;
  box-shadow: 0 -2px 2px #dddddd71;
  padding: 0.4rem;
  background-color: var(--theme-color);
  width: 100vw;
  position: absolute;
  left: 0;
  padding-bottom: 2rem;
}

/*  */
.to-comment {
  padding: 0.14rem 0.2rem;
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--theme-color);
  border-top: 1px solid var(--border-color);
}
.to-comment .user-icon {
  width: 1rem;
  height: 1rem;
}
.to-comment .v-input {
  background-color: var(--bg-color5);
}
.to-comment .v-input {
  flex: 1;
  margin: 0 0.2rem;
  padding: 0.16rem 0.4rem;
}
.to-comment .v-btn {
  padding: 0.16rem 0.5rem;
  border: none;
}
</style>