import { request } from "@/util/request";

// -----------------------------
/**
 * 
 * @param {Object} userInfo{
   username: "tom",
   phone: "134150000001",
   password: "pariatur",
   code: "232332",
 }; 
 * @returns 
 */
export function register({ username, phone, password, code }) {
  return request({
    method: "post",
    url: "/users/register",
    data: {
      username,
      phone,
      password,
      code,
    },
  });
}
// - phone
export function getRegisterCode(phone) {
  return request({
    method: "get",
    url: `/users/registerCode/${phone}`,
  });
}
/**
 * 
 * @param {number} phone
 * @param {number} code
 * @returns
 */
export function loginByCode(phone, code) {
  return request({
    method: "post",
    url: "/users/login",
    data: {
      phone,
      code,
      loginType: 1,
    },
  });
}
/**
 * -
 * @param {*} username
 * @param {*} password
 * @param {*} loginType 0
 * @returns
 */
export function loginByPwd(username, password) {
  return request({
    method: "post",
    url: "/users/login",
    data: {
      username,
      password,
      loginType: 0,
    },
  });
}
// -
export function getLoginCode(phone) {
  return request({
    method: "get",
    url: `/users/loginCode/${phone}`,
  });
}
// -
export function checkUser(token) {
  return request({
    method: "get",
    url: "/users/me",
    headers: {
      Authorization: token,
    },
  });
}

/**
 *  phone
 * @
 */
export function getUpdatePwdCode(username, phone) {
  return request({
    method: "get",
    url: `/users/forgetCode/${username}/${phone}`,
    // params: {
    //   phone,
    // },
  });
}

/**
 *  
 * @param {*} userInfo {username,phone,code}
 * @returns
 */
export function checkUpdatePwdCode({ username, phone, code }) {
  return request({
    method: "post",
    url: "/users/forgetCode/check",
    params: {
      username,
      phone,
      code,
    },
  });
}
//  phone newPassword
export function updatePwd(userInfo) {
  const { phone, newPassword } = userInfo;
  return request({
    method: "put",
    url: "/users/updatePassword",
    data: {
      phone,
      newPassword,
    },
  });
}

// ---------------------------------------
/**
 * 
 * @param {file:icon} fromData
 * @param {token:string} token
 * @returns res
 */
export function updateUserIcon(fromData, token) {
  return request({
    method: "put",
    url: "/users/update/icon",
    headers: {
      "Content-Type": "multipart/form-data",
      Authorization: token,
    },
    data: fromData,
    timeout: 10000,
  });
}

/**
 *  
 * @param {string} nickName 
 * @param {string} token
 * @returns
 */
export function updateNickName(nickname, token) {
  return request({
    method: "put",
    url: "/users/updateNickName",
    headers: {
      Authorization: token,
    },
    data: { nickname },
  });
}

/**
 *  
 * @param {string} username 
 * @param {string} token
 * @returns
 */
export function updateUserName(username, token) {
  return request({
    method: "put",
    url: `/users/updateUsername/${username}`,
    headers: { Authorization: token },
  });
}
/**
 * username
 * @param {*} token
 * @returns
 */
export function getIsNotUpdateUsername(token) {
  return request({
    method: "get",
    url: "/uses/isNotUpdateUsername",
    headers: { Authorization: token },
  });
}

/**
 *  byCode
 * @param {string} phone 
 * @param {string} code 
 * @param {string} token
 * @returns
 */
export function updatePhone(phone, code, token) {
  return request({
    method: "put",
    url: `/users/updatePhone/${phone}/${code}`,
    headers: {
      Authorization: token,
    },
    data: { phone },
  });
}
/**
 *  
 * @param {string} phone 
 * @param {string} token
 * @returns
 */
export function getUpdateCode(phone, token) {
  return request({
    method: "get",
    url: `/users/updateTelCode/${phone}`,
    headers: { Authorization: token },
  });
}

/**
 * 
 * @param {*} newPassword
 * @param {*} oldPassword
 * @param {String} token
 * @returns
 */
export function changeUserPwd(newPassword, oldPassword, token) {
  return request({
    method: "put",
    url: "/users/updatePassword",
    headers: {
      Authorization: token,
    },
    data: { newPassword, oldPassword },
  });
}

// 
export function exitLogin(token) {
  return request({
    method: "delete",
    url: "/users/logout",
    headers: { Authorization: token },
  });
}
