<template>
  <div class="comment-card comments" v-show="comment">
    <!--  -->
    <div class="lable-group" @click="!disableComment ? toCommentView() : ''">
      <div class="lable top">
        <van-image
          lazy-load
          round
          :src="getImgSrc(comment?.userIcon)"
          class="icon"
        />
        <div class="name">{{ comment?.nickname }}</div>
        <van-tag plain color="var(--tip-color2)" v-if="comment?.isMe"
          ></van-tag
        >
      </div>
      <div class="lable">{{ comment?.time }}</div>
    </div>
    <!--  -->
    <div
      class="lable contents"
      @click="!disableComment ? toCommentView() : ''"
      v-html="comment?.content"
    ></div>
    <!--  -->
    <div class="good-imgs" v-if="comment?.images">
      <van-image
        lazy-load
        class="img"
        fit="cover"
        v-for="(src, i) in comment?.images"
        :key="i"
        :src="getImgSrc(src)"
        @click="showPreImg(comment?.images, i)"
      />
    </div>

    <!--  -->
    <div class="lable-group tips">
      <div class="lable">
        <van-rate v-model="stars" readonly size="0.4rem" gutter="0" />
      </div>
      <div class="lable">
        <!--  -->
        <span
          v-if="comment?.isMe && !disableComment"
          class="text"
          @click="deleteOrderComment"
          ></span
        >
        <div class="left" @click="addCommentPick">
          <!--  -->
          <van-icon
            :name="comment?.isLiked ? 'like' : 'like-o'"
            :color="
              comment?.isLiked ? 'var(--tip-color2)' : 'var(--text-color3)'
            "
          />
          <span class="text">{{ comment?.liked || "" }}</span>
        </div>
        <!--  -->
        <div class="right" @click="changeCommentPopup" v-if="!disableComment">
          <van-icon name="comment-o" />
          <span class="text">{{ comment?.comment || "" }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { addCommentLiked } from "@/api/comment/comment";
import { getResourImageByName } from "@/api/res";
import { ImagePreview } from "vant";
import { mapState } from "vuex";
export default {
  props: {
    comment: {
      type: Object,
      default() {
        return {
          stars: 0,
        };
      },
    },
    disableComment: {
      required: false,
      default: false,
    },
  },
  name: "CommentCard",
  data() {
    return {
      pickTimer: "",
      stars: 0,
    };
  },
  watch: {
    "comment.stars": {
      deep: true,
      immediate: true,
      handler(newVal) {
        this.stars = newVal;
      },
    },
  },
  methods: {
    // 
    changeCommentPopup() {
      this.$store.commit("setShowCommentPopup", {
        show: true,
        commentId: this.comment.id,
      });
    },

    // 
    async showPreImg(imgs, i) {
      const images = JSON.parse(JSON.stringify(imgs));
      // 
      await images.forEach((p, i) => {
        images[i] = this.getImgSrc(p);
      });
      // 
      ImagePreview({ images, closeable: true, startPosition: i });
    },

    // 
    deleteOrderComment() {
      this.$emit("deleteOneOrderComment");
    },

    // 
    async addCommentPick() {
      if (this.pickTimer) return;
      const res = await addCommentLiked(
        this.comment.id,
        this.$store.getters.token
      );
      if (res.data.code === 20011) {
        if (this.comment.isLiked) {
          this.$set(this.comment, "isLiked", false);
          this.$set(this.comment, "liked", this.comment.liked - 1);
          this.$toast("");
        } else {
          this.$set(this.comment, "isLiked", true);
          this.$set(this.comment, "liked", this.comment.liked + 1);
        }
      }
    },

    // toCommentView
    toCommentView() {
      this.$router.push({
        name: "commentdetail",
        query: { id: this.comment.id },
        params: { animate: "forward", comment: this.comment },
      });
    },

    // 
    getImgSrc(url) {
      return getResourImageByName(url);
    },
  },
  computed: {
    ...mapState(["showCommentPopup"]),
  },
};
</script>

<style>
/*  */
.comments {
  display: flex;
  flex-direction: column;
}

.comments .top .icon {
  width: 1rem;
  height: 1rem;
}
.comments .top .name {
  float: left;
  padding: 0 0.2rem;
  color: var(--theme-color4);
}
.comments .contents {
  display: block;
  transition: 0.2s;
  max-height: 6em;
  text-overflow: ellipsis;
  overflow: hidden;
}
.good-imgs {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: hidden;
}
.good-imgs .img {
  display: block;
  width: 2rem;
  height: 2rem;
  margin: 0.2rem 0;
  margin-right: 0.3rem;
  border-radius: 8px;
  object-fit: cover;
  overflow: hidden;
}
.tips {
  cursor: pointer;
  color: var(--text-color4);
  padding: 0.1rem 0.2rem;
}
.tips .lable {
  color: var(--text-color4);
}
.tips .lable div {
  padding: 0 0.1rem;
}
.tips .lable > div {
  display: flex;
  align-items: center;
}
.tips .lable .text {
  user-select: none;
  font-size: 0.3rem;
  padding: 0 0.12rem;
}
</style>