import { request } from "@/util/request";

/**
 * 
 * @param {string} token
 * @returns
 */
export function getPurseInfo(token) {
  return request({
    method: "get",
    url: "/waller",
    headers: {
      Authorization: token,
    },
  });
}

/**
 *  
 * @param {Number} chargeType 
 * @param {Number} recharge 
 * @param {String} token
 * @returns
 */
export function reChargeByBombo(chargeType, rechargeComboId, token) {
  return request({
    method: "post",
    url: "/waller/recharge",
    headers: {
      Authorization: token,
    },
    data: {
      chargeType,
      rechargeComboId,
    },
  });
}

/**
 *  
 * @param {Number} chargeType 
 * @param {Number} recharge 
 * @param {String} token
 * @returns
 */
export function reChargeByValue(chargeType, rechargeAmount, token) {
  return request({
    method: "post",
    url: "/waller/recharge",
    headers: {
      Authorization: token,
    },
    data: {
      chargeType,
      rechargeAmount,
    },
  });
}
/**
 * 
 * @param {String} year
 * @param {String} month
 * @param {String} day
 * @param {string} token
 * @returns
 */
export function getBills(year, month, token) {
  return request({
    method: "get",
    url: `/waller/bill/${year}/${month}`,
    headers: { Authorization: token },
  });
}

/**
 * 
 * @param {string} token
 * @returns
 */
export function getRechargeCombo(token) {
  return request({
    method: "get",
    url: "/rechargeCombo/all",
    headers: { Authorization: token },
  });
}
