<template>
  <div>
    <!--  -->
    <TopNav :title="title" @click-left="onClickLeft" />

    <!--  -->
    <div class="froms">
      <van-form @submit="toSubmit">
        <!--  -->
        <van-field
          v-if="isUpdatePwd"
          v-model="user"
          name="user"
          type="text"
          left-icon="contact"
          placeholder=" "
          :rules="[{ required: true }]"
          class="v-input"
        />
        <van-field
          v-model="username"
          name="username"
          :type="isUserPwd ? 'text' : 'tel'"
          :left-icon="isUserPwd ? 'contact' : 'phone'"
          :placeholder="isUserPwd ? ' ' : ' '"
          :rules="[{ required: true }]"
          @blur="phoneCheck"
          class="v-input"
        />
        <van-field
          v-if="isUserPwd"
          v-model="password"
          type="password"
          left-icon="eye-o"
          name="password"
          placeholder=" "
          class="v-input"
          :rules="[{ required: true }]"
        />
        <van-field
          v-if="!isUserPwd"
          v-model="code"
          type="number"
          left-icon="label-o"
          name="code"
          placeholder=" "
          class="v-input"
          :rules="[{ required: true }]"
        >
          <template #button>
            <!--  -->
            <input
              type="button"
              class="right-btn"
              @click="getLoginCode"
              :disabled="codeFontDis"
              v-model="codeFont"
              :style="codeFontDis ? 'opacity:0.7;' : 'opacity:1;'"
            />
          </template>
        </van-field>
        <van-field
          v-if="isUpdatePwd && isUpdateCodeCheck"
          v-model="newPassword"
          type="password"
          left-icon="eye-o"
          name="password"
          placeholder=" "
          class="v-input"
          :rules="[{ required: true }]"
        />
        <!--  -->
        <div class="center-tips">
          <van-checkbox
            v-model="savePwd"
            icon-size="0.4rem"
            checked-color="var(--theme-color2)"
            v-show="!isUpdatePwd"
          >
            
          </van-checkbox>
          <div class="right" style="margin-left: auto">
            <div
              style="cursor: pointer"
              @click="
                {
                  isUserPwd = !isUserPwd;
                  isUpdatePwd = true;
                }
              "
              v-show="isUserPwd"
            >
              {{ "" }}&nbsp;|&nbsp;
            </div>
            <div
              style="cursor: pointer"
              @click="
                {
                  isUserPwd = !isUserPwd;
                  isUpdatePwd = false;
                }
              "
            >
              {{ isUserPwd ? "" : "" }}
            </div>
          </div>
        </div>
        <div class="btns" v-if="!isUpdatePwd">
          <input class="v-btn login-btn" type="submit" value="" />
          <input
            class="v-btn reg-btn"
            type="button"
            value=""
            @click="onClickRight"
          />
        </div>
        <div class="btns" v-else>
          <input
            v-if="!isUpdateCodeCheck"
            class="v-btn reg-btn update-btn"
            type="submit"
            value=""
          />
          <input
            v-else
            class="v-btn login-btn update-btn"
            type="submit"
            value=""
          />
        </div>
      </van-form>
    </div>
  </div>
</template>
<script>
import router from "@/router";
import TopNav from "@/components/TopNav.vue";
import {
  getLoginCode,
  loginByPwd,
  loginByCode,
  checkUser,
  getUpdatePwdCode,
  checkUpdatePwdCode,
  updatePwd,
} from "@/api/user/users";
export default {
  name: "LoginFrom",
  components: { TopNav },

  data() {
    return {
      title: " ",
      rTitle: "",
      isUserPwd: true,
      username: "",
      password: "",
      user: "", // 
      newPassword: "",
      code: "",
      codeFont: "",
      codeFontDis: false,
      savePwd: false, // 

      isUpdatePwd: false, // 
      isUpdateCodeCheck: false, // --
      phoneReg:
        /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/,
    };
  },

  mounted() {
    // 
    this.autoLogin();
  },
  methods: {
    // 
    toSubmit() {
      if (this.isUserPwd) {
        this.toLoginByPwd(); // 
      } else if (!this.isUpdatePwd && !this.isUpdatePwd) {
        this.toLoginByReg(); // 
      } else if (this.isUpdatePwd && !this.isUpdateCodeCheck) {
        this.checkUpdateCode(); // 
      } else {
        this.toForgetPwd(); // 
      }
    },
    // 1
    async toLoginByPwd() {
      let res = await loginByPwd(this.username, this.password);
      if (res.status === 200 && res.data.code === 20011) {
        this.reqUserInfo(res.data.data);
        // 
        this.$notify({ type: "success", message: "" });
        this.toView();
      } else {
        // console.log(res.data);
        this.$notify({
          type: "danger",
          message: this.isUserPwd ? "" : "",
        });
      }
    },

    // 2
    async toLoginByReg() {
      // 
      const res = await loginByCode(this.username, this.code);
      if (res.status === 200 && res.data?.code === 20011) {
        // 
        this.reqUserInfo(res.data.data);
        this.$notify({ type: "success", message: "" });
        this.toView();
      } else {
        this.$notify({
          type: "danger",
          message: res.data?.message ?? "",
        });
      }
    },
    // || 
    async getLoginCode() {
      if (!this.username) {
        return this.$notify("");
      } else if (!this.phoneCheck()) {
        return this.$notify("");
      }
      this.codeFontDis = true;
      let times = 60;
      let timer = setInterval(() => {
        times--;
        this.codeFont = times + "s";
        if (times === 0) {
          this.codeFont = "";
          this.codeFontDis = false;
          clearInterval(timer);
        } else {
          this.codeFont = times + "s";
        }
      }, 1000);
      //  ||  
      const res = this.isUpdatePwd
        ? await getUpdatePwdCode(this.user, this.username)
        : await getLoginCode(this.username);

      if (res.data.code === 20011) {
        // this.$notify({
        //   type: "success",
        //   message: `\n${res.data.data}`,
        //   duration: 5000,
        // });
        this.$notify({
          type: "success",
          message: `,`,
        });

        this.code = res.data.data; // 
      } else {
        this.$notify({
          type: "danger",
          message: ``,
        });
      }
    },

    // 3
    async toForgetPwd() {
      const res = await updatePwd({
        phone: this.username,
        newPassword: this.newPassword,
      });
      if (res.data?.code === 20011) {
        this.$notify({
          type: "success",
          message: ``,
          onClose: () => {
            this.password = "";
            this.newPassword = "";
            this.code = "";
            this.isUserPwd = true;
            this.savePwd = false;
            this.isUpdatePwd = false;
            this.isUpdateCodeCheck = false;
          },
        });
      } else {
        this.$notify({
          type: "danger",
          message: ``,
        });
      }
    },
    // 4--
    async checkUpdateCode() {
      if (this.isUpdatePwd) {
        const res = await checkUpdatePwdCode({
          username: this.user,
          phone: this.username,
          code: this.code,
        });
        if (res.status === 200 && res.data.code === 20011) {
          this.isUpdateCodeCheck = true;
          this.$notify({
            type: "success",
            message: ``,
          });
        } else {
          this.$notify({
            type: "danger",
            message: ``,
          });
        }
      }
    },
    // 
    toView() {
      if (this.$route.params.toBack) {
        this.$router.back();
      } else {
        this.$router.push({
          name: "home",
          params: { animate: "forward" },
        });
      }
    },

    // --
    reqUserInfo(token) {
      // 
      this.$store.commit("setLoginState", true); // vuex
      this.$store.commit("setToken", { token, savePwd: this.savePwd });
      //  
      checkUser(token).then((res) => {
        if (res.data.code === 20011) {
          this.$store.commit("setUserInfo", res.data.data);
        } else {
          this.$store.commit("loginOut"); // 
          this.password = "";
          this.newPassword = "";
          this.code = "";
          this.isUserPwd = true;
          this.savePwd = false;
          this.isUpdatePwd = false;
          this.isUpdateCodeCheck = false;
        }
      });
    },
    // 
    onClickLeft() {
      router.back();
    },
    onClickRight() {
      router.replace({
        name: "regist",
        params: { animate: "forward" },
      });
    },
    // 
    phoneCheck() {
      if (!this.isUserPwd) {
        if (this.username !== "" && !this.phoneReg.test(this.username.trim())) {
          this.$notify({ type: "danger", message: "" });
          return false;
        }
        return this.phoneReg.test(this.username.trim());
      }
    },

    // 
    async autoLogin() {
      // 
      if (this.$route.params?.username)
        this.username = this.$route.params.username;
      // 
      const token =
        localStorage.getItem(this.$store.state.TOKEN_NAME) ||
        sessionStorage.getItem(this.$store.state.TOKEN_NAME) ||
        "";

      this.$store.commit("setToken", token); // vuextoken
      if (token === "") return;
      const res = await checkUser(token);
      if (!res.data.code === 20011 || res.status !== 200) {
        this.password = "";
        this.$store.commit("loginOut"); // 
        this.$toast({ type: "fail", message: "" });
        return;
      }
      let second = 2;
      let timer;
      // 
      if (res.data?.code === 20011) {
        this.password = "..*****...";
        this.savePwd = true;
        this.username = res.data.data.username;
        this.$dialog
          .alert({
            message: "...",
            confirmButtonText: "",
          })
          .then(() => {
            clearInterval(timer);
            this.password = "";
            this.$store.commit("loginOut"); // 
          });

        timer = setInterval(() => {
          second--;
          if (!second) {
            this.$store.commit("setUserInfo", res.data.data);
            this.toView(); // 
            this.$dialog.close(); // 
          }
        }, 1000);
      }
    },
  },
  watch: {
    isUpdatePwd(newVal) {
      if (newVal) {
        this.title = "";
      } else {
        this.title = " ";
      }
    },
  },
};
</script>
<style scoped>
.center-tips {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 0.38rem;
  margin: 0.3rem 0.2rem;
  color: rgba(0, 0, 0, 0.9);
}
.center-tips .right {
  display: flex;
  align-items: center;
}
.btns {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 0.8rem;
}
.btns .update-btn {
  width: 100%;
  padding: 0.3rem;
  font-size: 0.45rem;
}
.reg-btn {
  border: 2px solid #eee;
  background-color: #fff;
  box-shadow: #eee -3px -3px 5px, #eee 3px 3px 5px;
}
.reg-btn:hover {
  box-shadow: none;
}
.reg-btn:active {
  box-shadow: none;
}
</style>
