import Vue from "vue";
// home
import VueRouter from "vue-router";
import HomeView from "@/views/home/HomeView";
import MyView from "@/views/home/MyView.vue";
import ShopcartView from "@/views/home/ShopcartView.vue";
import CategoryView from "@/views/home/CategoryView.vue";
// 
import LoginView from "@/views/login/LoginView.vue";
import RegisterView from "@/views/login/RegisterView.vue";
// 
import CollectView from "@/views/second/CollectView.vue";
import SearchView from "@/views/second/SearchView.vue";
import EventView from "@/views/second/EventView.vue";
import RechargeView from "@/views/second/RechargeView";
import PurseView from "@/views/second/PurseView";
import UserView from "@/views/second/UserView";
import InfoView from "@/views/second/InfoView";
import SigninView from "@/views/second/SigninView";
import AddressView from "@/views/second/AddressView";
import BillsView from "@/views/second/BillsView";
import BillsDetailView from "@/views/second/BillsDetailView";
// 
import GoodsDetail from "@/views/goods/GoodsDetail.vue";
// 
import CommentDetail from "@/views/comment/CommentDetail";
import CommentView from "@/views/comment/CommentView";
import AddCommentView from "@/views/comment/AddCommentView";

// 
import OrderView from "@/views/orders/OrderView.vue";
import CheckOrder from "@/views/orders/CheckOrder";

import store from "@/store";
Vue.use(VueRouter);
const routes = [
  // 1.
  {
    path: "/",
    component: HomeView,
    children: [
      {
        path: "",
        redirect: "/home",
        meta: {
          title: " ",
          lv: 1,
          keepAlive: true,
        },
      },
      {
        path: "home",
        name: "home",
        meta: {
          title: " ",
          lv: 1,
          keepAlive: true,
        },
      },
    ],

    meta: {
      title: " ",
      lv: 1,
      keepAlive: true,
    },
  },
  // 1.1 
  {
    path: "/search",
    name: "search",
    component: SearchView,
    meta: {
      title: "",
      lv: 2,
    },
  },
  // 1.2 
  {
    name: "active",
    path: "/events",
    component: EventView,
    meta: {
      title: "",
      lv: 2,
    },
  },
  // 2.
  {
    name: "category",
    path: "/category",
    component: CategoryView,
    meta: {
      title: "",
      lv: 1,
      keepAlive: true,
    },
  },

  // 3.
  {
    name: "shopcar",
    path: "/shopcar",
    component: ShopcartView,
    meta: {
      title: "",
      lv: 1,
      keepAlive: true,
    },
  },

  // 4.
  {
    name: "my",
    path: "/my",
    component: MyView,
    meta: {
      title: "",
      lv: 1,
      keepAlive: true,
    },
  },
  // 4.1 
  {
    name: "order",
    path: "/my/order",
    component: OrderView,
    meta: {
      title: "",
      lv: 2,
      keepAlive: true,
      permission: true,
    },
  },
  // 4.2 
  {
    name: "collect",
    path: "/my/collect",
    component: CollectView,
    meta: {
      title: "",
      lv: 2,
      keepAlive: true,
      permission: true,
    },
  },
  // 4.3.
  {
    name: "recharge",
    path: "/my/recharge",
    component: RechargeView,
    meta: {
      title: "",
      lv: 2,
      keepAlive: true,
      permission: true,
    },
  },
  // 4.4 
  {
    path: "/my/signin",
    name: "signin",
    component: SigninView,
    meta: {
      title: "",
      lv: 2,
      permission: true,
    },
  },
  // 4.4.
  {
    name: "purse",
    path: "/my/purse",
    component: PurseView,
    meta: {
      title: "",
      lv: 2,
      keepAlive: true,
      permission: true,
    },
  },
  // 4.5 
  {
    name: "user",
    path: "/my/user",
    component: UserView,
    meta: {
      title: "",
      lv: 2,
      permission: true,
    },
  },
  {
    name: "userinfo",
    path: "/my/info",
    component: InfoView,
    meta: {
      title: "",
      lv: 2,
      permission: true,
    },
  },
  // 4.6 
  {
    name: "bills",
    path: "/my/bills",
    component: BillsView,
    meta: {
      title: "",
      lv: 2,
      keepAlive: true,
      permission: true,
    },
  },
  // 4.6.2 
  {
    name: "billsdetail",
    path: "/my/bills/detial",
    component: BillsDetailView,
    meta: {
      title: "",
      lv: 2,
      permission: true,
    },
  },
  // 4.7 
  {
    name: "address",
    path: "/address",
    component: AddressView,
    meta: {
      title: "",
      lv: 2,
      permission: true,
    },
  },

  // 1
  {
    name: "login",
    path: "/login",
    component: LoginView,
    meta: {
      title: "",
      lv: 2, // 
    },
  },
  // 2
  {
    name: "regist",
    path: "/regist",
    component: RegisterView,
    meta: {
      title: "",
      lv: 2,
      keepAlive: true,
    },
  },

  // 5.
  {
    name: "detail",
    path: "/detail",
    component: GoodsDetail,
    meta: {
      title: "",
      lv: 2,
      // permission: true,
    },
  },

  // 5.1 
  {
    name: "commentdetail",
    path: "/comments/detail",
    component: CommentDetail,
    meta: {
      title: "",
      lv: 2,
      permission: true,
    },
  },
  // 5.1.b 
  {
    name: "comment",
    path: "/comments",
    component: CommentView,
    meta: {
      title: "",
      lv: 2,
      keepAlive: true,
      permission: true,
    },
  },

  // 5.2 
  {
    path: "/checkorder",
    name: "checkorder",
    component: CheckOrder,
    meta: {
      title: "",
      lv: 2,
      permission: true,
    },
  },

  // 6 
  {
    path: "/comment/add",
    name: "addcomment",
    component: AddCommentView,
    meta: {
      title: "",
      lv: 2,
      permission: true,
    },
  },

  // 404
  {
    name: "notFund",
    path: "/404",
    component: res => require(["@/views/NotFund.vue"], res),
  },
];

const router = new VueRouter({
  // mode: "history",
  mode: "hash",
  base: process.env.BASE_URL,
  routes,
});
// 
router.beforeEach((to, from, next) => {
  // 
  document.title = to.meta?.title || " ";
  // 
  if (to.meta.permission) {
    // 
    if (store.getters.token) {
      next();
    } else {
      next({
        name: "login",
        params: {
          animate: "forward",
          toBack: true,
        },
      });
    }
  } else {
    next();
  }
});

export default router;
