<template>
  <!--  -->
  <van-list
    class="comment-list"
    v-model="loading"
    :error.sync="isHttpError"
    :finished="finished"
    finished-text=""
    @load="onLoad"
  >
    <transition-group name="bottomRight" tag="div">
      <!--  -->
      <comment-card
        :comment="item"
        v-for="(item, i) in commentList"
        :key="item.id"
        class="v-card item"
        @deleteOneOrderComment="deleteOneOrderComment(item.id, i)"
      />
    </transition-group>
  </van-list>
</template>
<script>
import { getGoodCommentById } from "@/api/comment/comment";
import CommentCard from "./CommentCard.vue";
import { delteOrderComment } from "@/api/comment/ordercomment";
export default {
  components: { CommentCard },
  props: {
    reqType: {
      // 
      type: Number,
      required: false,
      default: 0,
    },
    gid: {
      // 
      required: true,
    },
  },
  name: "CommentList",
  data() {
    return {
      currentPage: 1, // 
      commentList: [], // 
      isHttpError: false, // 
      loading: false, // 
      finished: false, // 
    };
  },
  methods: {
    // 
    onLoad() {
      this.reqCommentListBySort();
    },

    // 
    deleteOneOrderComment(id, index) {
      // console.log(id, index);
      this.$dialog
        .confirm({
          title: "",
          beforeClose: async (action, done) => {
            if (action === "confirm") {
              const res = await delteOrderComment(
                id,
                this.$store.getters.token
              );
              if (res.data.code === 20011) {
                this.commentList.splice(index, 1); // 
                done();
                this.$toast({ type: "success", message: "" });
              } else {
                this.$toast({ type: "fail", message: "" });
              }
            }
            done();
          },
        })
        .catch(() => {});
    },

    // 
    reqCommentListBySort() {
      this.loading = true;
      getGoodCommentById(this.gid, this.reqType, this.$store.getters.token)
        .then((res) => {
          // console.log(res.data);
          if (res.data.code === 20011) {
            const data = res.data.data;
            let count = 0;
            let timer = setInterval(() => {
              // 
              if (count >= data.length) {
                this.finished = true;
                clearInterval(timer);
                this.timer = "";
                return;
              }
              this.commentList.push(data[count]);
              count++;
              // 
              this.loading = false;
              this.finished = true;
            }, 200);
          } else {
            this.isHttpError = true;
          }
        })
        .catch(() => {
          this.isHttpError = true;
        });
    },

    // 
    reStart() {
      this.currentPage = 1;
      this.commentList.splice(0); // 
      this.onLoad();
    },
  },
};
</script>
<style scoped>
.item {
  border-radius: 10px;
  margin: 0.2rem 0.1rem;
  box-shadow: var(--shadow-color3);
  padding: 0.2rem 0.3rem;
}
</style>