<template>
  <van-pull-refresh
    style="min-height: 90vh"
    v-model="isRefreshing"
    @refresh="reStart"
  >
    <!--  -->
    <van-list
      class="orders-list"
      v-model="loading"
      :error.sync="isHttpError"
      :finished="finished"
      finished-text=""
      @load="onLoad"
    >
      <transition-group name="sliceInZoomOut" tag="div">
        <div
          :key="order?.orderId"
          class="v-card order-card"
          v-for="(order, i) in orderList"
        >
          <!--  -->
          <div class="flex-c-c top" v-if="order?.orderItems?.length">
            <span class="left"
              ><van-icon name="shop" color="var(--text-color3)" />
              </span
            >
            <span class="right" style="color: var(--tip-color2)">{{
              getState(order)
            }}</span>
          </div>
          <!--  -->
          <div
            class="good-list"
            v-if="order?.orderItems"
            @click="toView('', order)"
          >
            <goods-info
              class="goods"
              v-for="p in order.orderItems"
              :key="p?.gid"
              :goods="p"
              :price="p?.price"
              :tips="p?.isPay"
              :props="getPorps(p)"
              :num="p.quantity"
            />
          </div>
          <!--  -->
          <div class="all-price" v-if="order?.orderItems?.length">
            <small>( {{ order?.orderItems.length }} )</small>
            <span class="price">{{ getPrice(order.price) }}</span>
          </div>
          <!--  -->
          <!--  -->
          <div class="btns" v-if="getState(order) == ''">
            <button
              class="v-click btn cancel"
              @click="cancelOrder(order?.orderId, i)"
            >
              
            </button>
            <button
              class="v-click btn cancel"
              @click="toView('update', order, i)"
            >
              
            </button>
            <button class="v-click btn" @click="toView('unpaid', order, i)">
              
            </button>
          </div>
          <!--  -->
          <div class="btns" v-if="getState(order) === ''">
            <button class="v-click btn cancel" @click="toastDeliver">
              
            </button>
          </div>
          <!--  -->
          <div class="btns" v-if="getState(order) === ''">
            <button
              class="v-click btn"
              @click="checkDeliverd(order?.orderId, i)"
            >
              
            </button>
          </div>
          <!--  -->
          <div class="btns" v-if="getState(order) === ''">
            <button
              class="v-click btn cancel"
              @click="deleteOrder(order?.orderId, i)"
            >
              
            </button>
            <button class="v-click btn" @click="toAddComment(order?.orderId)">
              
            </button>
          </div>
          <!--  -->
          <!-- <div class="btns" v-if="getState(order) === ''"></div> -->
        </div>
      </transition-group>
    </van-list>
  </van-pull-refresh>
</template>
<script>
import {
  getAllOrder,
  getDeliveredOrder,
  getDoneOrder,
  getUnDeliverOrder,
  getUnPaidOrder,
  cancelOrderById,
  doneOrder,
  deleteOrderById,
} from "@/api/order/order";
import currency from "currency.js";
import GoodsInfo from "../Goods/GoodsInfo.vue";
export default {
  components: { GoodsInfo },
  props: {
    reqType: {
      // 
      type: String,
      required: true,
      default: "all",
    },
  },
  name: "OrderList",
  data() {
    return {
      orderList: [], // 
      isHttpError: false, // 
      loading: false, // 
      finished: false, // 
      isRefreshing: false, // 
      comment: "", // 
      typeColor: "var(--text-color4)",
    };
  },
  methods: {
    // 
    async onLoad() {
      if (!this.reqType) return;
      let res = null;
      this.loading = true;
      switch (this.reqType) {
        case "all":
          res = await getAllOrder(this.$store.getters.token); // 
          break;
        case "unpaid": // 
          res = await getUnPaidOrder(this.$store.getters.token);
          break;
        case "undeliver": // 
          res = await getUnDeliverOrder(this.$store.getters.token);
          break;
        case "delivered": // 
          res = await getDeliveredOrder(this.$store.getters.token);
          break;
        case "comment": // 
          res = await getDoneOrder(this.$store.getters.token);
          break;
      }
      // console.log(res.data);
      if (res.status === 200 && res.data.code === 20011) {
        let count = 0;
        const data = res?.data?.data;
        // 
        if (data.length === 0) {
          this.finished = true;
          this.loading = false;
          return;
        }
        let timer = setInterval(() => {
          data[count].reqType = this.reqType === "all" ? "" : this.reqType;
          this.orderList.push(data[count]);
          count++;
          if (count >= data.length) {
            this.loading = false;
            this.finished = true;
            clearInterval(timer);
            timer = "";
          }
        }, 300);
      } else {
        this.isHttpError = true;
        this.finished = true;
        this.loading = false;
      }
    },
    // 
    getPorps(p) {
      return `${p?.color || ""} ${p?.size || ""} ${p?.edition || ""} ${
        p?.combo || ""
      }`;
    },

    // 
    getState(p) {
      if (!p?.isPay) return;
      let res = "";
      if (p.isPay === "") {
        res = "";
      } else if (p.logisticsStatus === "") {
        res = "";
      } else if (p.logisticsStatus === "" && p.status === "") {
        res = "";
      } else if (p.logisticsStatus === "" && p.status === "") {
        res = "";
      } else {
        res = "";
      }
      return res;
    },
    // En
    getStateEn(p) {
      if (!p?.isPay) return;
      let res = "";
      if (p.isPay === "") {
        res = "unpaid";
      } else if (p.logisticsStatus === "") {
        res = "undeliver";
      } else if (p.logisticsStatus === "" && p.status === "") {
        res = "delivered";
      } else if (p.logisticsStatus === "" && p.status === "") {
        res = "uncomment";
      } else {
        res = "comment";
      }
      return res;
    },

    // 
    getPrice(price) {
      return currency(price);
    },

    // 
    reStart() {
      this.finished = false;
      this.orderList.splice(0); // 
      new Promise((resolve) => {
        if (this.isRefreshing) this.onLoad();
        resolve(true);
      }).then(() => {
        setTimeout(() => {
          this.isRefreshing = false;
        }, 1600);
      });
    },

    // 1
    //  
    async cancelOrder(id, i) {
      if (id === "") return;
      this.$dialog
        .confirm({
          title: "",
          beforeClose: async (action, done) => {
            if (action === "confirm") {
              const res = await cancelOrderById(
                id,
                this.comment,
                this.$store.getters.token
              );
              if (res.status === 200 && res.data.code === 20011) {
                this.$toast({
                  message: "",
                  duration: 1000,
                  type: "success",
                });
                this.orderList.splice(i, 1);
              } else {
                this.$toast("");
              }
              done();
            }
            done();
          },
        })
        .catch(() => {});
    },

    // 2
    // 
    toastDeliver() {
      this.$dialog({ title: "" });
    },

    // 3
    checkDeliverd(id, i) {
      if (id === "") return;
      this.$dialog
        .confirm({
          title: "",
          beforeClose: async (action, done) => {
            if (action === "confirm") {
              const res = await doneOrder(id, this.$store.getters.token);
              if (res.status === 200 && res.data.code === 20011) {
                done();
                this.$toast({
                  message: "",
                  duration: 500,
                  type: "success",
                });
                this.orderList.splice(i, 1);
              } else {
                this.$toast("");
              }
            } else {
              done();
            }
          },
        })
        .catch(() => {});
    },

    // type, order, index
    toView(orderType, order) {
      if (orderType === "") {
        orderType = this.getStateEn(order);
      }
      // console.log(orderType);
      this.$router.push({
        name: "checkorder",
        params: {
          animate: "forward",
        },
        query: {
          info: order,
          goodsList: order?.orderItems,
          isOrder: true,
          orderType,
        },
      });
    },

    // 4
    async sureOrder(id, i) {
      if (id === "") return;
      this.$dialog
        .confirm({
          title: "",
          beforeClose: async (action, done) => {
            if (action === "confirm") {
              const res = await doneOrder(id, this.$store.getters.token);
              if (res.status === 200 && res.data.code === 20011) {
                done();
                this.$toast({
                  message: "~",
                  duration: 500,
                  type: "success",
                });
                this.orderList.splice(i, 1);
              } else {
                this.$toast("");
              }
            } else {
              done();
            }
          },
        })
        .catch(() => {});
    },

    // 5
    toAddComment(id) {
      this.$router.push({
        name: "addcomment",
        params: {
          animate: "forward",
        },
        query: {
          id,
        },
      });
    },

    // 
    async deleteOrder(id, i) {
      if (id === "") return;
      this.$dialog
        .confirm({
          title: "",
          message: "",
          beforeClose: async (action, done) => {
            if (action === "confirm") {
              const res = await deleteOrderById(id, this.$store.getters.token);
              if (res.status === 200 && res.data.code === 20011) {
                this.$toast({
                  message: "",
                  duration: 500,
                  type: "success",
                });
                this.orderList.splice(i, 1);
              } else {
                this.$toast("");
              }
              done();
            }
            done();
          },
        })
        .catch(() => {});
    },
    // 6
    toOrderComment(id) {
      console.log(id);
      this.$router.push({
        name: "commentdetail",
        params: {
          animate: "forward",
        },
        query: {
          id,
        },
      });
    },
  },
};
</script>

<style scoped>
/*  */
.orders-list {
  margin-top: 0.2rem;
  padding: 0.1rem;
  transition: all 0.4s;
  animation-delay: var(--router-delay) / 2;
}
/*  */
.order-card .top {
  text-align: right;
  padding-bottom: 0.24rem;
  justify-content: space-between;
}
.order-card .top span {
  font-size: 0.36rem;
}
/*  */
.orders-list .order-card {
  width: 100%;
  margin-bottom: 0.4rem;
  box-shadow: var(--shadow-color3);
  backdrop-filter: blur(20px);
  border-radius: 10px;
}
.order-card .goods {
  margin: 0.2rem 0;
}
.all-price {
  /* border-top: 1px solid #eeeeee8b;
  border-bottom: 1px solid #eeeeee8b; */
  padding: 0.2rem;
  text-align: right;
}
.all-price .price {
  font-size: 0.56rem;
}
/*  */
.btns {
  margin-top: 0.2rem;
  display: flex;
  justify-content: right;
}
.btns .btn {
  background-color: var(--tip-color2);
  box-shadow: none;
  border-radius: 4px;
  color: var(--text-color2);
  padding: 0.1rem 0.2rem;
  transition: 0.2s;
  font-size: 0.3rem;
  margin-left: 0.2rem;
}
.btns .cancel {
  border-radius: 4px;
  background-color: var(--theme-color3);
  color: var(--text-colo4);
  border: 1px solid var(--text-color3);
}
.btns .btn:hover,
.btns .btn:active {
  filter: brightness(0.9);
}

/*  */
.orders-list >>> .van-list__loading,
.orders-list >>> .van-list__finished-text,
.orders-list >>> .van-list__error-text {
  width: 100%;
  font-size: 0.35rem;
}
</style>