import { request } from "@/util/request";

/**
 *  
 * @param {string} id
 * @param {string} token
 * @returns
 */
export function getGoodCommentById(goodsId, sort, token) {
  return request({
    method: "get",
    url: `/evaluation/query`,
    headers: {
      Authorization: token,
    },
    params: {
      goodsId,
      sort: sort || 0, // 01
    },
  });
}

/**
 *  
 * @param {string} id
 * @param {string} token
 * @returns
 */
export function addCommentLiked(id, token) {
  return request({
    method: "put",
    url: `/evaluation/liked/${id}`,
    headers: {
      Authorization: token,
    },
  });
}
