<template>
  <div class="v-card good-card" @click="toGoodsDetail(item.goods.id)">
    <!--  -->
    <van-image class="bg" lazy-load :src="getImgSrc(item.goods.images[0])">
      <template v-slot:loading>
        <van-loading type="spinner" size="24" />
      </template>
    </van-image>
    <!--  -->
    <div class="bottom">
      <div class="title">{{ item.goods.name }}</div>
      <div class="price">
        <span class="tip">{{ item.price }}</span>
      </div>
      <div class="sales"> {{ item.goods.sale }}</div>
      <van-icon
        name="shopping-cart-o"
        class="lf-tip"
        size="0.4rem"
        color="var(--text-color)"
      />
    </div>
  </div>
</template>
<script>
import { getResourImageByName } from "@/api/res";
export default {
  props: ["item"],
  name: "SortClass",
  data() {
    return {};
  },
  methods: {
    // 
    toGoodsDetail(id) {
      this.$router.push({
        name: "detail",
        query: { id },
        params: { animate: "forward" },
      });
    },
  },
  computed: {
    // 
    getImgSrc() {
      return (url) => {
        return getResourImageByName(url);
      };
    },
  },
};
</script>
<style scoped>
.good-card {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  padding: 0;
  overflow: hidden;
  border-radius: 10px;
  margin: 0.2rem 0;
  backdrop-filter: blur(20px);
  background-color: rgba(255, 255, 255, 0.8);
}
.bg {
  display: block;
  width: 100%;
  height: 4rem;
  object-fit: cover;
}
/*  */
.bottom {
  padding: 0.2rem 0.25rem;
}
.bottom .title {
  font-size: 0.4rem;
}

.price {
  font-size: 0.4rem;
  padding-top: 0.1rem;
  color: var(--tip-color);
}
.price .tip {
  font-size: 0.55rem;
}
.sales {
  font-size: 0.3rem;
  opacity: 0.7;
}
.bottom .lf-tip {
  position: absolute;
  right: 0.3rem;
  bottom: 0.3rem;
  border: none;
  box-shadow: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
