<template>
  <!--  -->
  <van-popup
    transition="popup"
    class="icon-popup"
    closeable
    round
    v-model="show"
    duration="0.24"
    @open="open"
    position="center"
    :style="{ width: '80%', height: '8rem', margin: '0 auto' }"
  >
    <div class="flex-col icon-ctn">
      <span class="title"></span>
      <van-uploader
        class="update"
        accept="image/png, image/jpeg, image/jpg,"
        v-model="imgList"
        preview-size="3rem"
        :max-count="1"
        :max-size="500 * 1024"
        @oversize="overSizeFile"
        :preview-options="{ closeable: true }"
        :after-read="uploadIcon"
      />
      <div class="btn-group">
        <input
          type="button"
          @click="show = false"
          value=""
          class="v-btn v-cancel"
        />
        <input
          type="submit"
          @click="updateIconPathInfo"
          value=""
          class="v-btn"
        />
      </div>
    </div>
  </van-popup>
</template>

<script>
import { mapState } from "vuex";
import { updateUserIcon } from "@/api/user/users";
import { getResourImageByName } from "@/api/res";
export default {
  name: "ChangeIcon",
  props: ["value"],
  data() {
    return {
      show: false,
      imgList: [], // 
      uploadPath: "", // 
      isUpload: false,
    };
  },
  methods: {
    // 
    async uploadIcon(file) {
      if (!file?.content) {
        return (this.show = false);
      }

      this.isUpload = true;
      file.status = "uploading";
      file.message = "...";
      // 
      const fileRes = await updateUserIcon(
        this.imgList[0],
        this.$store.getters.token
      );
      // 
      if (fileRes.status !== 200 || fileRes.data.code != 20011) {
        file.status = "failed";
        file.message = "";
        return this.$toast({ type: "fail", message: "" });
      } else {
        // 
        this.uploadPath = fileRes.data.data;
        file.status = "done";
        file.message = "";
        this.$notify({ type: "success", message: "" });
        this.isUpload = false;
        this.$set(this.userInfo, "icon", this.uploadPath);
      }
    },

    // 
    async updateIconPathInfo() {
      if (this.isUpload) {
        return this.$toast("...");
      }
      this.show = false;
    },

    // 
    open() {
      if (this.userIcon) {
        this.$set(this.imgList, [0], { url: this.userIcon });
      }
    },
    // 
    overSizeFile() {
      this.$toast(` 500kb`);
    },
  },
  computed: {
    ...mapState(["userInfo"]),
    userIcon() {
      if (this.userInfo?.icon) {
        return getResourImageByName(this.userInfo.icon);
      } else {
        return "";
      }
    },
  },
  watch: {
    value(newVal) {
      this.show = newVal;
    },
    show(newVal) {
      this.$emit("input", newVal);
    },
  },
};
</script>

<style scpoed>
/*  */
.icon-ctn {
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 0.3rem;
  height: 100%;
  width: 100%;
}
.icon-ctn .title {
  font-size: 0.6rem;
  letter-spacing: 0.1em;
  font-weight: 600;
  margin: 0.3rem 0;
}
.btn-group {
  display: flex;
  width: 80%;
  margin: 0.2rem 0;
  justify-content: space-around;
}
.btn-group .v-btn {
  padding: 0.15rem 0.6rem;
}
</style>