<template>
  <default-page title="">
    <div class="nav-bar"></div>
    <van-tabs
      sticky
      class="tabs"
      v-model="active"
      swipeable
      animated
      line-width="1.2rem"
      color="var(--theme-color2)"
      title-active-color="var(--theme-color2)"
    >
      <!--  -->
      <van-tab v-for="(p, i) in option" :key="i" :title="p.title">
        <!--  -->
        <order-list :reqType="p.type" />
      </van-tab>
    </van-tabs>
  </default-page>
</template>
<script>
import DefaultPage from "@/components/DefaultPage.vue";
import OrderList from "@/components/Order/OrderList.vue";
export default {
  components: { DefaultPage, OrderList },
  name: "OrderView",
  props: { index: { required: false, default: 0 } },
  data() {
    return {
      active: 0,
      option: [
        { title: "", type: "all" },
        { title: "", type: "unpaid" },
        { title: "", type: "undeliver" },
        { title: "", type: "delivered" },
        { title: "", type: "comment" },
      ],
    };
  },
  mounted() {
    // 
    if (this.index !== undefined) {
      this.active = this.index;
    }
  },
};
</script>
<style scoped>
.tabs {
  width: 100%;
  height: auto;
  margin-top: -10px;
}
</style>