import { request } from "@/util/request";

/**
 * 
 * @param {number} page
 * @param {number} size
 * @param {"name": "string","cid": "string","recommend": 0,"isNew": 0,"saleSort": "string","priceSort": "string"} data
 * @returns
 *  name: "string",
    cid: "string",
    recommend: 0, // 0, 10
    isNew: 0, // 10
    saleSort: "Asc", // Asc/Des,
    priceSort: "Asc", // Asc/Des,
 */
export function getGoodsListByPageSize(page, size, type) {
  return request({
    method: "post",
    url: `/goods/query/${page}/${size}`,
    data: type,
  });
}

/**
 * id
 * @param {*} id
 * @returns
 */
export function getGoodDetailById(id) {
  return request({
    method: "get",
    url: `/goods/query/${id}`,
  });
}

/**
 * @
 * @returns
 */
// 
export function getFirstSortList() {
  return request({
    method: "get",
    url: "/goodsCategory/query/one",
  });
}
// 
export function getSecondSortList(id) {
  return request({
    method: "get",
    url: `/goodsCategory/query/child/${id}`,
  });
}

// 
export function getAllSortList() {
  return request({
    method: "get",
    url: `/goodsCategory/select`,
  });
}
