<template>
  <div class="wind-init goods-detail">
    <!--  -->
    <share-nav />
    <div class="contents" v-if="!isError">
      <!--  -->
      <van-swipe @change="onChangeGoodImg">
        <van-swipe-item v-for="(img, i) in item?.goods?.images" :key="i">
          <van-image lazy-load :src="getImgSrc(img)" class="imags" />
        </van-swipe-item>
        <template #indicator>
          <div class="custom-indicator">
            {{ current + 1 }}/{{ item?.goods?.images?.length }}
          </div>
        </template>
      </van-swipe>
      <!--  -->
      <div class="content">
        <!--  -->
        <div class="v-card top">
          <div class="price">
            
            <span class="big">{{ getPrice(item?.price) || "0.00" }}</span>
            <span class="small"
              >:{{ getPrice(item?.price) || "0.00" }}</span
            >
          </div>
          <div class="title">{{ item?.goods?.name }}</div>
          <div class="lable-group">
            <div class="lable">{{ item?.goods?.sale }}</div>
            <div class="lable">
              <van-icon name="location-o" />
              {{ item?.goods?.city }}
            </div>
          </div>
        </div>
        <!--  -->
        <div class="v-card center">
          <div class="lable-group" @click="showProps = true">
            <span class="lable"></span>
            <div class="lable black props">
              <span class="box" ref="selectOption">{{ getOption }}</span>
            </div>
            <van-icon name="arrow" />
          </div>
          <!--  -->
          <div class="lable-group">
            <span class="lable"></span>
            <div class="flex-col w-80">
              <span class="lable black" style="padding-bottom: 0.2rem"
                >{{ item?.goods?.city }} | {{
                  item?.goods?.postage ? `${item.goods.postage} ` : ""
                }}</span
              >
              <span class="lable"></span>
            </div>
            <van-icon name="arrow" />
          </div>
          <!--  -->
          <div class="lable-group">
            <span class="lable"></span>
            <div class="flex-col w-80">
              <span class="box">{{ getSafeInfo }}</span>
            </div>
            <van-icon name="arrow" />
          </div>
        </div>
        <!--  -->
        <div
          @click="comments.length ? toView(5) : ''"
          class="v-card comments animate__animated animate__fadeIn"
        >
          <div class="lable-group">
             ({{ comments.length }})
            <div
              class="lable"
              style="color: var(--tip-color2); font-size: 0.3rem"
            >
              <van-icon
                name="arrow"
                color="var(--tip-color2)"
                class="icon"
              />
            </div>
          </div>
          <!--  -->
          <comment-card
            :comment="comments[0]"
            v-if="comments.length"
            :disable-comment="true"
          />
        </div>
      </div>

      <!--  -->
      <div class="detail-info">
        <div class="title"><span>&emsp;&emsp;</span></div>
        <div class="label label1">{{ getSafeInfo }}</div>
        <!-- <van-image
          lazy-load
          :src="getImgSrc(item?.goods?.images)"
          class="img"
        /> -->
      </div>

      <!--  -->
      <van-sku
        ref="skucart"
        v-model="showProps"
        :sku="initSku"
        :goods="{ picture: getImgSrc(goodProps?.defaultOption?.icon) }"
        :goods-id="GOOD_ID"
        :initial-sku="skuDefault"
        :quota="0"
        :stock-threshold="10"
        @sku-selected="selectedSku"
        @buy-clicked="toMakeOder"
        @add-cart="addShopCar"
        stepper-title=""
      />
      <!--  -->
      <div class="shop-nav" v-if="!isError">
        <van-goods-action>
          <van-goods-action-icon
            @click="$toast('')"
            icon="chat-o"
            text=""
            color="#ee0a24"
          />
          <van-goods-action-icon
            icon="cart-o"
            text=""
            @click="toView(2)"
          />
          <van-goods-action-icon
            :icon="isCollect ? 'star' : 'star-o'"
            :text="isCollect ? '' : ''"
            color="#ff5000"
            @click="isCollect ? deleteCollect() : addCollect()"
          />
          <van-goods-action-button
            type="warning"
            text=""
            @click="showProps = true"
          />
          <van-goods-action-button
            type="danger"
            text=""
            @click="showProps = true"
          />
        </van-goods-action>
      </div>
    </div>
    <!--  -->
    <error-card
      v-if="isError"
      @refresh="location.reload()"
      :image="'network'"
      text=""
    />
  </div>
</template>

<script>
// res
import { getGoodDetailById } from "@/api/good/good";
import { getGoodProps, getGoodPropsById } from "@/api/good/props";
import {
  getTheCollectByGid,
  addCollectByGid,
  deleteCollectByGid,
} from "@/api/user/collect";
import { getResourImageByName } from "@/api/res";
import { getGoodCommentById } from "@/api/comment/comment";
import { addShopCart } from "@/api/shopcart/shopcart";
// components
import ShareNav from "@/components/Detail/ShareNav.vue";
import { Dialog, Toast } from "vant";
import { mapState } from "vuex";
import CommentCard from "@/components/Comment/CommentCard.vue";
import ErrorCard from "@/components/ErrorCard.vue";

export default {
  components: { ShareNav, CommentCard, ErrorCard },
  name: "GoodsDetail",
  data() {
    return {
      isError: false, // 
      GOOD_ID: this.$route.query?.id || "",
      // 
      item: {}, // 
      goodProps: {}, //
      newOption: {}, // 

      // 
      current: 0, // 
      isCollect: false, // 
      comments: [], // 

      // 
      showProps: false,
      allStock: "", // 

      combos: [],
      sizes: [],
      colors: [],
      editions: [],
      rows: [],

      initSku: {},
      skuDefault: {}, // 
      finalSelectVal: {}, // 
    };
  },
  mounted() {
    this.getGoodDetail(); // 
    this.getGoodProps(); // 
  },
  watch: {
    // token
    token: {
      immediate: true,
      deep: true,
      handler() {
        this.getCommentList(); // 
        this.getTheCollect(); // 
      },
    },
    // sku
    finalSelectVal() {
      this.$refs.selectOption.innerHTML = this.finalSelectVal;
    },
  },

  methods: {
    // 
    getGoodDetail() {
      // 
      if (this.GOOD_ID === "") return (this.isError = true);
      getGoodDetailById(this.GOOD_ID)
        .then((res) => {
          this.isError = !res.data.code === 20011;
          if (res.data.code === 20011) {
            res.data.data.goods.images = res.data.data.goods.images.split(",");
            this.item = res.data.data;
          }
        })
        .catch(() => {
          this.isError = true;
        });
    },

    // 
    async getGoodProps() {
      // 
      if (this.GOOD_ID === "") return (this.isError = true);
      const res = await getGoodPropsById(this.GOOD_ID);
      if (res.status === 200 && res.data.code === 20011) {
        this.goodProps = res.data.data;
        // 
        this.initProps();
      } else {
        this.isError = true;
      }
    },

    // 
    selectedSku(info) {
      const { selectedSkuComb } = info;
      if (!selectedSkuComb) return;
      this.finalSelectVal = selectedSkuComb.props;
    },

    // 
    toMakeOder({ selectedSkuComb, selectedNum }) {
      selectedSkuComb.price = selectedSkuComb.price / 100;
      this.$router.push({
        name: "checkorder",
        params: {
          animate: "forward",
          toBack: "true",
        },

        query: {
          // 
          info: {
            price: selectedNum * selectedSkuComb.price, // 
          },

          goodsList: [
            {
              ...this.item.goods,
              images: selectedSkuComb.icon, // 
              id: selectedSkuComb.id, // id
              gid: this.item.goods.id,
              props: selectedSkuComb.props, // 
              quantity: selectedNum, // 
              price: selectedSkuComb.price, // 
              unitPrice: selectedSkuComb.price, //
              postage: this.item.goods.postage,
            },
          ],
        },
      });
    },

    // 
    async addShopCar(info) {
      if (!this.$store.getters.token) return Toast("");
      const res = await addShopCart(
        info.selectedSkuComb.id,
        info.selectedNum,
        this.$store.getters.token
      );
      // console.log(res.data);
      if (res.status === 200 && res.data.code === 20011) {
        Toast("");
      } else {
        Toast("");
      }
      this.showProps = false;
    },

    // 
    initProps() {
      const { defaultOption, combo, colorAndIcon, size, edition } =
        this.goodProps;

      const price = Number(defaultOption.price).toFixed(2);
      const props = {
        tree: [], // row
        list: [], // col

        price, // 
        stock_num: 0,
        listIndex: "", // 0
        hide_stock: false, // 
      };

      // 
      let row = 0;
      if (combo.length > 0) {
        row++;
        let row1 = {
          k: "",
          k_s: "combo",
          v: [],
          largeImageMode: false, //  
        };
        // 
        combo.forEach((p, i) => {
          row1.v.push({
            id: i,
            name: p.combo,
          });
          this.combos.push(p.combo);
        });
        this.rows.push("combo");
        props.tree.push(row1);
      }

      // 
      if (colorAndIcon.length > 0) {
        row++;
        let row2 = {
          k: "", // 
          k_s: "color",
          v: [], // 
        };
        colorAndIcon.forEach((p, i) => {
          row2.v.push({
            id: i,
            name: p.color,
            imgUrl: p.icon ? this.getImgSrc(p.icon) : "",
            previewImgUrl: p.icon ? this.getImgSrc(p.icon) : "",
          });
          this.colors.push(p.color);
        });
        this.rows.push("color");
        props.tree.push(row2);
      }

      // 
      if (size.length > 0) {
        row++;
        let row3 = {
          k: "", // 
          k_s: "size",
          v: [], // 
        };
        size.forEach((p, i) => {
          row3.v.push({
            id: i,
            isSellOut: p.isSellOut !== "",
            // name: p.size + ` ${p.isSellOut || ""}`,
            name: p.size,
          });
          this.sizes.push(p.size);
        });
        this.rows.push("size");
        props.tree.push(row3);
      }

      // 
      if (edition.length > 0) {
        row++;
        row;
        let row4 = {
          k: "", // 
          // k_s: `row${row}`,
          k_s: "edition",
          v: [], // 
        };
        edition.forEach((p, i) => {
          row4.v.push({
            id: i,
            name: p.edition,
            imgUrl: this.getImgSrc(p.icon),
            previewImgUrl: this.getImgSrc(p.icon),
          });
          this.editions.push(p.edition);
        });
        this.rows.push("edition");
        props.tree.push(row4);
      }
      // const _this = this; // vue
      // 
      const GOOD_ID = this.goodProps.defaultOption.gid;
      const rows = this.rows;
      const goodsPropsList = {
        color: this.colors,
        size: this.sizes,
        combo: this.combos,
        edition: this.editions,
      };

      // 
      function getList(tree, _this) {
        let res = [];
        let group = 1; // 
        for (let i = 0; i < tree.length; i++) {
          group = tree[i].v ? group * tree[i].v.length : group;
        }
        const item = {
          selectedNum: 0,
        }; // 
        let indexCol = 0;
        let allStock = 0;

        // 
        callBack(tree, item, indexCol, allStock, rows);

        // 
        async function callBack(tree, item, indexCol, allStock, rows) {
          for (let i = 0; i < tree[indexCol].v.length; i++) {
            // row-value
            item = JSON.parse(JSON.stringify(item));
            item[rows[indexCol]] = i;
            if (res.find((p) => item.id === p.id)) continue;
            //  ()
            if (indexCol + 1 === tree.length) {
              // obj
              const obj = {};
              for (let k = 0; k < rows.length; k++) {
                obj[rows[k]] = "";
              }
              for (const key in obj) {
                obj[key] = goodsPropsList[key][item[key]];
                item.props = (item?.props || "") + `${obj[key]} `;
              }

              // id
              const { color, combo, size, edition } = obj;
              const http = await getGoodProps(
                GOOD_ID,
                color,
                combo,
                size,
                edition
              );
              if (http.status === 200 && http.data.data) {
                const p = http.data.data.defaultOption;
                item.id = String(p.id);
                item.price = p.price * 100;
                item.stock_num = +p.stock;
                item.gid = String(p.gid);
                item.icon = p.icon;
                if (item.stock_num > 0 && props.listIndex === "") {
                  props.listIndex = res.length; // 
                  _this.skuDefault = JSON.parse(JSON.stringify(item));
                }
                props.stock_num = props.stock_num + item.stock_num;

                res.push(JSON.parse(JSON.stringify(item)));
                item.id = "";
                item.props = "";
              }
              continue; // 
            }
            // 
            indexCol++;
            await callBack(tree, item, indexCol, allStock, rows); // 
          }
        }
        return res;
      }
      // console.log(props);

      // 
      props.list = getList(props.tree, this);
      this.initSku = props;
      // this.$nextTick(() => {
      //   this.skuDefault = props[props.listIndex];
      // });
    },

    // 
    getTheCollect() {
      if (this.GOOD_ID === "") return (this.isError = true);
      getTheCollectByGid(this.GOOD_ID, this.$store.getters.token)
        .then((res) => {
          this.isCollect = res.data.code === 20011;
        })
        .catch(() => {
          this.isError = true;
        });
    },
    // 
    addCollect() {
      // 
      if (!this.isLoginState) return this.toView(4);
      addCollectByGid(this.GOOD_ID, this.$store.getters.token)
        .then((res) => {
          this.isCollect = true;
          res.data.code === 20011 ? Toast(" ") : Toast(" ");
        })
        .catch(() => {});
    },
    // 
    deleteCollect() {
      // 
      if (!this.isLoginState) return this.toView(4);

      Dialog.confirm({ title: "" })
        .then(() => {
          deleteCollectByGid(this.GOOD_ID, this.$store.getters.token)
            .then((res) => {
              if (res.data.code === 20011) {
                this.isCollect = false;
                Toast(" ");
              } else {
                Toast(" ");
              }
            })
            .catch(() => {
              Toast(" ");
            });
        })
        .catch(() => {});
    },

    // 
    getCommentList() {
      getGoodCommentById(this.GOOD_ID, 0, this.$store.getters.token)
        .then((res) => {
          if (res.data.code === 20011) {
            const data = res.data.data;
            data.forEach((p) => {
              this.comments.push(p);
            });
          }
        })
        .catch(() => {});
    },

    // 
    toView(i) {
      switch (i) {
        // 
        case 1:
          this.$router.push({
            name: "search",
            params: { animate: "forward" },
          });
          break;
        // 
        case 2:
          this.$router.push({
            name: "shopcar",
            params: { animate: "forward" },
          });
          break;
        // 
        case 3:
          this.$router.push({
            name: "order",
            params: { animate: "forward" },
          });
          break;
        // 
        case 4:
          this.$router.push({
            name: "login",
            params: { animate: "forward" },
          });
          break;
        // 
        case 5:
          this.$router.push({
            name: "comment",
            query: { id: this.GOOD_ID, size: this.comments.length },
            params: { animate: "forward" },
          });
          break;
        // 
        case 6:
          this.$router.push({
            name: "commentdetail",
            query: { id: this.GOOD_ID },
            params: { animate: "forward" },
          });
          break;
      }
    },

    // 
    getPrice(price) {
      return ((price * 1000 * this.item.discount) / 1000).toFixed(2);
    },

    // 
    onChangeGoodImg(index) {
      this.current = index;
    },

    // 
    getImgSrc(url) {
      return getResourImageByName(url);
    },
  },

  computed: {
    ...mapState(["token", "isLoginState"]),

    // 
    getOption() {
      if (this.goodProps?.defaultOption) {
        const p = this.goodProps?.defaultOption;
        return `${p?.color ?? ""}  ${p?.size ?? ""}  ${p?.combo ?? ""} `;
      }
      return "";
    },

    // 
    getSafeInfo() {
      const p = this.item?.goods;
      if (!p) return "";
      let res = [];
      if (p?.warrantyTime > 0) res.push(` ${p.warrantyTime}`);
      if (p.changerTime > 0) res.push(` ${p.changerTime}`);
      if (p.refundTime > 0) res.push(` ${p.refundTime}`);
      return res.toString();
    },
  },
};
</script>


<style scoped>
.goods-detail {
  background-color: var(--bg-color6);
  display: flex;
  flex-direction: column;
  padding: 0;
  padding-bottom: 1.2rem;
}
/*  */
.custom-indicator {
  position: absolute;
  right: 0.4rem;
  bottom: 0.6rem;
  padding: 0.1rem 0.2rem;
  letter-spacing: 0.1em;
  font-size: 0.3rem;
  color: rgba(255, 255, 255, 0.8);
  background-color: rgba(128, 128, 128, 0.4);
  backdrop-filter: blur(20px);
  border-radius: 0.8rem;
}
.imags {
  width: 100%;
  height: 10rem;
  object-fit: contain;
  border-radius: 8px;
  overflow: hidden;
}
/*  */
.content {
  margin-top: -0.42rem;
  border-radius: 10px;
  padding: 0.25rem;
  background-color: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(20px);
  z-index: 1;
}
.v-card {
  padding: 0.2rem 0.3rem;
  background-color: var(--theme-color3);
  margin: 0.15rem 0;
}
.top .price {
  font-size: 0.4rem;
  color: var(--tip-color2);
  padding: 0.2rem 0;
  font-family: Helvetica;
}
.price .big {
  font-size: 0.7rem;
}
.price .small {
  color: var(--text-color3);
  font-size: 0.3rem;
  padding-left: 0.1rem;
  text-decoration: line-through;
}
.top .title {
  font-weight: 600;
  font-size: 0.45rem;
}
.shop-nav {
  z-index: 999;
}
/*  */
.lable-group {
  padding: 0.2rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.lable-group .lable {
  color: var(--text-color3);
  font-size: 0.32rem;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.lable-group .black {
  color: var(--text-color4);
}
/*  */
.lable-group .props {
  width: 80%;
}
.lable-group .box {
  cursor: pointer;
  display: inline-block;
  max-width: calc(100% - 1.6rem);
  color: var(--text-color3);
  background-color: var(--bg-color5);
  border-radius: 0.4em;
  padding: 0.08rem 0.15rem;
  font-size: 0.25rem;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
/*  */
.comments {
  position: relative;
}

/*  */
.detail-info {
  width: 100%;
  padding: 0 0.3rem;
  border-radius: 10px;
}
.detail-info .title {
  text-align: center;

  padding: 0 0.4rem;
  margin: 0.3rem 0;
  color: var(--border-color);
}
.detail-info .title span {
  color: var(--text-color4);
}
.detail-info .label {
  padding: 0.2rem;
}
.detail-info .label1 {
  width: 100%;
  text-align: center;
  /* box-shadow: var(--shadow-color3); */
  margin-bottom: 0.4rem;
  border-radius: 8px;
  background-color: var(--theme-color);
}
.detail-info .img {
  width: 100%;
  border-radius: 8px;
  overflow: hidden;
}
</style>
