import { request } from "@/util/request";
import qs from "qs";
/**
 * 
 * @param {string} token
 * @returns
 */
export function getDefaultAddress(token) {
  return request({
    method: "get",
    url: "/address/default",
    headers: {
      Authorization: token,
    },
  });
}

/**
 * 
 * @param {string} token
 * @returns
 */
export function getAllAddress(token) {
  return request({
    method: "get",
    url: "/address/all",
    headers: {
      Authorization: token,
    },
  });
}

/**
 * id
 * @param {string} token
 * @returns
 */
export function getAddressById(id, token) {
  return request({
    method: "get",
    url: `/address/query/${id}`,
    headers: {
      Authorization: token,
    },
  });
}

/**
 * 
 * @param {string} token
 * @param {object} addressInfo 
    "name": "string",
    "phone": "string",
    "province": "string",
    "city": "string",
    "district": "string",/
    "address": "string",
    "isDefault": 0 
 * @returns
 */
export function putAddres(addressInfo, token) {
  const { name, phone, province, city, district, address, isDefault } = addressInfo;
  return request({
    method: "post",
    url: `/address/save`,
    headers: { Authorization: token },
    data: { name, phone, province, city, district, address, isDefault: +isDefault },
  });
}

/**
 * 
 * @param {string} token 
 * @param {object} updateInfo 
 *    id: 0,id
      name: "string",
      phone: "string",
      province: "string",
      city: "string",
      district: "string",
      address: "string",
      isDefault: 0,
 * @returns 
 */
export function updateAddress(item, token) {
  const { uid, id, name, phone, province, city, district, address, isDefault } = item;
  return request({
    method: "put",
    url: `/address/update/${id}`,
    headers: { Authorization: token },
    data: {
      uid,
      name,
      phone,
      province,
      city,
      district,
      address,
      isDefault: +isDefault,
    },
  });
}

/**
 * 
 * @param {*} id Id
 * @param {*} token
 * @returns
 */
export function deleteAddressById(id, token) {
  return request({
    method: "delete",
    url: `/address/delete/${id}`,
    headers: { Authorization: token },
  });
}

/**
 * 
 * @param {Array} ids Id
 * @param {String} token
 * @returns
 */
export function deleteAddressByIdsArray(ids, token) {
  return request({
    method: "delete",
    url: "/address/deletes",
    headers: { Authorization: token },

    params: { ids },
    paramsSerializer: {
      serialize(params) {
        return qs.stringify(params, { indices: false });
      },
    },
  });
}
