<template >
  <van-pull-refresh
    style="min-height: 90vh"
    v-model="isRefresh"
    :head-height="60"
    @refresh="onReflesh"
  >
    <div class="cart-list">
      <!--  -->
      <van-list
        class="list"
        v-if="!isEmpty"
        v-model="loading"
        :finished="finished"
        finished-text=""
        @load="getAllShopCartList"
      >
        <!-- 1)  -->
        <van-checkbox-group v-model="selectList" @change="changeSelectArr">
          <transition-group name="sliceInZoomOut" tag="div">
            <!--  -->
            <cart-card
              v-bind="$attrs"
              v-for="(item, i) in cartList"
              :showSku="showSku"
              :key="item.id"
              :item="item"
              :index="i"
              @onSelectProps="onSelectProps(item?.goodsId, item?.id)"
              @deleteCartByOne="deleteCartByOne"
            >
              <template #checkbox>
                <!-- 2) -->
                <van-checkbox
                  icon-size="0.5rem"
                  :name="i"
                  checked-color="var(--tip-color2)"
                />
              </template>
            </cart-card>
          </transition-group>
        </van-checkbox-group>
      </van-list>
      <!--  -->
      <goods-sku
        @updataShopcart="updataShopcart"
        :GOOD_ID="GOOD_ID"
        v-model="showSku"
        :cartId="shopcartId"
      />
    </div>
  </van-pull-refresh>
</template>
<script>
import CartCard from "@/components/ShopCart/CartCard.vue";
import { getAllShopCart, deleteOneShopCart } from "@/api/shopcart/shopcart";
import GoodsSku from "@/components/GoodsSku.vue";
export default {
  components: { CartCard, GoodsSku },
  name: "CartList",
  data() {
    return {
      isRefresh: false, // 
      loading: false,
      finished: false,
      isEmpty: false,
      selectList: [], // 
      cartList: [], // 

      // gid
      GOOD_ID: "",

      // id
      shopcartId: "",

      // 
      showSku: false,
    };
  },
  methods: {
    // 1)  
    // 
    async getAllShopCartList() {
      this.loading = true;
      const res = await getAllShopCart(this.$store.getters.token);
      if (res.status === 200 && res.data.code === 20011) {
        const data = res.data.data;
        if (data.length === 0) {
          this.loading = false;
          this.finished = true;
          return;
        }
        let count = 0;
        let timer = setInterval(() => {
          const p = data[count];
          p.props = `${p?.color || ""} ${p?.size || ""} ${p?.edition || ""} ${
            p?.combo || ""
          }`;
          this.cartList.push(p);
          count++;
          if (count >= data?.length) {
            this.loading = false;
            this.finished = true;
            clearInterval(timer);
            timer = "";
            this.$emit("getCartLength", data);
          }
        }, 100);
      } else if (res.status === 200 && !res.data.code === 20011) {
        this.isEmpty = true;
      } else {
        this.isEmpty = true;
      }
    },
    // 
    changeSelectArr(arr) {
      this.$emit("changeSelectArr", arr, this.cartList);
    },

    /**
     * @
     */
    onReflesh() {
      this.cartList.splice(0);
      this.isRefresh = true;
      this.getAllShopCartList();
      this.isRefresh = false;
    },

    /**
     * @
     */
    updataShopcart(cartId, info) {
      const { props, nums, icon } = info;
      this.cartList.forEach((p, i) => {
        if (p.id === cartId) {
          this.$set(this.cartList[i], "props", props);
          this.$set(this.cartList[i], "quantity", nums);
          this.$set(this.cartList[i], "icon", icon);
        }
      });
    },

    /**
     * @
     */
    async deleteCartByOne(id, i) {
      this.$toast.loading({
        message: "...",
        forbidClick: true,
        duration: 0,
      });
      const res = await deleteOneShopCart(id, this.$store.getters.token);
      if (res.status === 200 && res.data.code === 20011) {
        this.cartList.splice(i, 1);
        this.$toast.clear();
      } else {
        this.$toast.fail("");
      }
    },

    // 2)  
    onSelectProps(GOOD_ID, shopcartId) {
      this.showSku = true; // 
      this.GOOD_ID = GOOD_ID; // id
      this.shopcartId = shopcartId; // id
    },
  },

  watch: {
    // 
    cartList: {
      immediate: true,
      deep: true,
      handler(val) {
        this.$emit("changeSelectArr", this.selectList, val);
        this.$emit("getCartLength", this.cartList); // 
      },
    },
    // 
    selectList: {
      deep: true,
      handler() {
        this.$emit(
          "changeSelectAll",
          this.selectList.length > 0 &&
            this.selectList.length === this.cartList.length
        );
      },
    },
  },
};
</script>
<style scoped>
.cart-list {
  margin-top: 1.4rem;
  padding-top: 0.2rem;
  padding-bottom: 3.4rem;
  border-radius: 12px 12px 0 0;
  background-color: var(--theme-color);
  border-top: 1px solid var(--border-color);
  /* box-shadow: var(--shadow-color4); */
}
.item {
  /* box-shadow: var(--shadow-color3); */
  box-shadow: none;
  background-color: var(--theme-color);
  margin: 0.3rem 0.1rem;
}
</style>