<template>
  <div class="share-view">
    <div class="top-nav-bak"></div>
    <!--  -->
    <div class="top-nav">
      <!--  -->
      <van-icon
        @click="$router.back()"
        name="arrow-left"
        class="icon"
        size="0.6rem"
      />
      <div class="right">
        <van-icon @click="toView(1)" name="search" class="icon" size="0.6rem" />
        <van-icon
          @click="showShare = true"
          name="ellipsis"
          class="icon"
          size="0.6rem"
        />
      </div>
    </div>
    <!--  -->
    <div class="share">
      <!--  -->
      <van-share-sheet
        v-model="showShare"
        title=""
        :options="options"
        @select="onSelect"
      />
      <!--  -->
      <van-dialog v-model="showDialog" title="">
        <div class="qrcode" ref="qecodeImg"></div>
      </van-dialog>
    </div>
  </div>
</template>
<script>
import { Dialog, Toast } from "vant";
import QRCode from "qrcodejs2";
import { copyTextAsync } from "@/util/copy";
export default {
  name: "ShareNav",
  data() {
    return {
      // 
      showShare: false,
      showDialog: false,
      sqrcode: "",
      options: [
        { name: "", icon: "wechat" },
        { name: "", icon: "link" },
        { name: "", icon: "poster" },
        { name: "", icon: "qrcode" },
      ],
    };
  },
  methods: {
    // 
    toView(i) {
      switch (i) {
        // 
        case 1:
          this.$router.push({
            name: "search",
            params: { animate: "forward" },
          });
          break;
      }
    },

    // 
    onSelect(p) {
      switch (p.icon) {
        case "qrcode":
          this.showDialog = true;
          this.$nextTick(() => {
            if (this.sqrcode === "") {
              this.sqrcode = new QRCode(this.$refs.qecodeImg, {
                text: location.href, // 
                correctLevel: QRCode.CorrectLevel.H,
              });
            }
          });
          break;
        default:
          copyTextAsync(location.href)
            .then(() => {
              Dialog({
                title: " ",
                message: location.href,
              });
            })
            .catch(() => {
              Toast(" ");
              Dialog({
                title: " ",
                message: location.href,
              });
            });
          break;
      }
      this.showShare = false;
    },
  },
};
</script>
<style scoped>
/*  */
.top-nav-bak {
  z-index: 10;
  width: 100%;
  height: 1.28rem;
  backdrop-filter: blur(20px);
  display: flex;
  justify-content: space-between;
  padding: 0.2rem;
  background-color: transparent;
}
.top-nav {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  /* background-color: rgba(182, 182, 182, 0.1); */
  background-image: radial-gradient(transparent 1px, var(--theme-color) 1px);
  background-size: 4px 4px;
  backdrop-filter: saturate(50%) blur(10px);
  border-bottom: 1px solid var(--theme-color);
  display: flex;
  justify-content: space-between;
  padding: 0.2rem;
  overflow: visible;
}

.top-nav .icon {
  padding: 0.14rem;
  transition: 0.3s;
  border-radius: 4px;
}
.top-nav .icon:hover,
.top-nav .icon:active {
  background-color: var(--bg-color3);
  transform: scale(0.85);
}

/*  */
.van-dialog {
  width: 65%;
}
.qrcode {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.2rem;
}
.qrcode >>> img {
  margin-top: 20px;
  width: 70%;
  height: 70%;
}
</style>