import { request } from "@/util/request";

/**
 * 
 * @param {*} token
 * @returns
 */
export function getAllCollectByGid(token) {
  return request({
    method: "get",
    url: `/collection/query`,
    headers: {
      Authorization: token,
    },
  });
}

/**
 * 
 * @param {*} gid
 * @param {*} token
 * @returns
 */
export function getTheCollectByGid(gid, token) {
  return request({
    method: "get",
    url: `/collection/query/${gid}`,
    headers: {
      Authorization: token,
    },
  });
}

/**
 * 
 * @param {string} gid
 * @param {string} token
 * @returns
 */
export function addCollectByGid(gid, token) {
  return request({
    method: "post",
    url: `/collection/save/${gid}`,
    headers: {
      Authorization: token,
    },
  });
}

/**
 * 
 * @param {string} gid
 * @param {string} token
 * @returns
 */
export function deleteCollectByGid(gid, token) {
  return request({
    method: "delete",
    url: `/collection/delete/${gid}`,
    headers: {
      Authorization: token,
    },
  });
}
